<template>
  <div>
    <h2>大事记登记</h2>

    <el-row>
      <el-button type="warning" @click="dialogVisible = true; needadd(); ">新增</el-button>

      <el-dialog title="新增大事记" :visible.sync="dialogVisible" width="25%">
        <el-form ref="form" :model="form" label-width="60px">
          <el-form-item label="日期：">
            <el-input v-model="form.thetime"></el-input>
          </el-form-item>
          <el-form-item label="标题：">
            <el-input v-model="form.ctitle"></el-input>
          </el-form-item>
          <el-form-item label="内容：">
            <el-input v-model="form.content"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false ;sumbitaddRow();">确 定</el-button>
      </span>
      </el-dialog>


      <el-button type="success" class="el-button-fabu"  @click="open">发布</el-button>
    </el-row>

    <el-table
        border
        :data="tableData"
        style="width: 100%">
      <el-table-column
          type="index"
          label="序号 "
          width="50">
      </el-table-column>
      <el-table-column
          label="日期"
          prop="thetime"
          width="120">
      </el-table-column>
      <el-table-column
          label="标题"
          prop="ctitle"
          width="200">
      </el-table-column>
      <el-table-column
          label="内容"
          prop="content">
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="info"
              @click="handleEdit(scope.$index, scope.row)">修改</el-button>

          <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :visible.sync="centerDialogVisible">
      <el-form ref="form" :model="editForm" label-width="60px">
        <el-form-item label="日期：">
          <el-input v-model="editForm.thetime"></el-input>
        </el-form-item>
        <el-form-item label="标题：">
          <el-input v-model="editForm.ctitle"></el-input>
        </el-form-item>
        <el-form-item label="内容：">
          <el-input v-model="editForm.content"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button @click="centerDialogVisible=false">取消</el-button>
        <el-button type="primary" @click="sumbitEditRow()">确定</el-button>
      </div>
    </el-dialog>
    <el-button-group>
      <el-button type="primary" icon="el-icon-arrow-left"  size="mini">上一页</el-button>
      <el-button type="primary"  size="mini">下一页<i class="el-icon-arrow-right el-icon--right" ></i></el-button>
    </el-button-group>
  </div>
</template>
<script>
let _index;
let a_index;
export default {
  name: "companyevents",
  data() {
    return {
      dialogVisible:false,
      centerDialogVisible:false,
      form:[],
      editForm:[],
      tableData: [{
        thetime: '2020-12-30',
        ctitle: '关于vue',
        content: 'vue的描述',
      }, {
        thetime: '2020-12-30',
        ctitle: '关于Java',
        content: 'Java的描述',
      }, {
        thetime: '2020-12-30',
        ctitle: '关于python',
        content: 'python的描述',
      }, {
        thetime: '2020-12-30',
        ctitle: '关于c',
        content: 'c的描述',
      }],
    }
  },
  methods: {
    handleEdit(index, row) {
      // console.log(index, row);
      this.centerDialogVisible = true
      this.editForm = Object.assign({}, row);//重置对象
      _index = index;
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    needadd(){
      this.tableData.push(this.form);
    },
    sumbitEditRow() {
      const editData = _index;
      console.log("editData的值"+	this.editForm)
      this.tableData[editData].thetime = this.editForm.thetime;
      this.tableData[editData].ctitle = this.editForm.ctitle;
      this.tableData[editData].content = this.editForm.content;
      this.centerDialogVisible = false;
      console.log("数据："+this.editForm.date)
      console.log("对象数组",this.tableData)
    },
    sumbitaddRow() {
      const addData = a_index;
      console.log("addData的值"+	this.Form)
      this.tableData[addData].thetime = this.Form.thetime;
      this.tableData[addData].ctitle = this.Form.ctitle;
      this.tableData[addData].content = this.Form.content;
      this.dialogVisible = false;
      console.log("数据："+this.Form.date)
      console.log("对象数组",this.tableData)
    },
    open() {
      this.$confirm('', '是否进行发布操作', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '发布成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消发布'
        });
      });
    }
  }
}
</script>

<style scoped>
.el-button-group{
  margin-top: 15px;
}
.el-table{
  margin-top: 15px;
}
.el-button-fabu{
  margin-left: 15px;
}
</style>