<template>
  <div class="main">
    <div class="search" v-width="widthData">
      <p>职位/公司</p>
      <input type="text">
      <button style="outline: none">搜索</button>
    </div>
    <div class="hot" v-width="widthData">
      <ul>
        <li v-for="item in list" :key="item">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
name: "search",
  data(){
  return{
    list:['热门职位：','销售','JAVA','会计','人事','文员','行政','出纳','平面设计','客服']
  }
  },
  props:{
    // 搜索框的宽度，默认50%，可任意传值
    widthData:{
      type:String,
      default:'938px'
    },
  },


  directives: {
    'width':{
      bind(el,binding) {
        el.style.width=binding.value
      },
    }
  },
}
</script>

<style scoped>
.main{
  width: 1200px;
  height: 80px;
  margin: 0 auto 10px;
}
.main .search p{
  width: 80px;
  font-size: 15px;
  color: black;
  margin-left: 10px;
}
.search{
  width: 938px;
  height: 46px;
  border: 1px solid #1AC9A6;
  margin: 13px auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
input{
  width: 762px;
  height: 20px;
  outline: none;
  border: 0;
  font-size: 18px;
  background: #f2f5f4;
}
button{
  font-size: 18px;
  color: #FFF;
  border: 0;
  background: #00b38a;
  width: 140px;
  height: 100%;
}
.hot{
  height: 46px;
  display: flex;
  align-items: center;
  justify-content: left;
  margin: 0 auto;
}
li:nth-of-type(1){
  text-align: left;
  color: gray;
  font-weight: 500;
}
li{
  list-style: none;
  float: left;
  padding: 0px 10px;
  font-size: 15px;
  color: #1AC9A6;
 margin-bottom: 5px;
}
li:hover{
  cursor: pointer;
}
li:not(:first-child):hover{
  text-decoration:underline;
}
</style>