<template>
  <div>
    <div class="edit">
      <el-row>
        <el-col :span="22">
          <h1>自我评价</h1>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" icon="el-icon-edit" @click="handleAddClick" >增加</el-button>
        </el-col>
      </el-row>
    </div>
    <div class="tab">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" :index="indexMethod" width="80">
        </el-table-column>
        <el-table-column prop="evltime" label="评价时间" width="220">
        </el-table-column>
        <el-table-column prop="evltitle" label="评价标题" width="220">
        </el-table-column>
        <el-table-column prop="evlmessage" label="评价内容"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="deleteRow(scope.$index,tableData)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5,10,15,20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="自我评价"
      :visible.sync="addBox"
      width="40%"
      :before-close="handleClose"
    >
      <div>
        <form action="sessions" accept-charset="UTF-8" method="POST">
          <div class="inputrestyle inputprepend">
            <input
              type="text"
              placeholder="评价时间"
              class="inputarea"
              style="border-radius: 4px"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="评价标题"
              class="inputarea"
              style="border-radius: 4px"
            />
          </div>
          <div class="inputprepend">
            <textarea
              type="text"
              placeholder="评价内容"
              class="inputarea"
              style="border-radius: 4px; padding: 15px 24px; height: 60px"
            ></textarea>
          </div>
          <div>
            <input
              type="submit"
              class="saveSubmit"
              name=""
              id=""
              value="保存"
            />
          </div>
        </form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          evltime: "2020-06",
          evltitle: "xxx",
          evlmessage: "优秀..........",
        },
        {
          evltime: "2020-09",
          evltitle: "xxx",
          evlmessage: "优秀..........",
        },
        {
          evltime: "2020-12",
          evltitle: "xxx",
          evlmessage: "优秀..........",
        },
        {
          evltime: "2021-3",
          evltitle: "xxx",
          evlmessage: "优秀..........",
        },
      ],
      addBox: false,
      page:1,
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    deleteRow(index,rows){
      rows.splice(index,1);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClose(done) {
      done();
    },
    handleAddClick() {
      this.addBox = true;
    },
  },
};
</script>

<style scoped>
::v-deep .el-table th,
::v-deep .el-table td {
  text-align: center !important;
}
.icon {
  margin: 10px 0px;
}
</style>

<style >
.edit {
  padding: 20px;
}
</style>
