<template>
    <Table border :columns="columns12" :data="data6">
        <template slot-scope="{ row }" slot="name">
            <strong>{{ row.name }}</strong>
        </template>
        <template slot-scope="{row,index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">查看</Button>
            <Button type="error" size="small" @click="remove(index)">删除</Button>
        </template>
    </Table>
</template>
<script>
    export default {
        data () {
            return {
                columns12: [
                    {
                        title: '姓名',
                        slot: 'name'
                    },
                    {
                        title: '性别',
                        key: 'sex'
                    },
                    {
                        title: '出生年月',
                        key: 'address'
                    },
                    {
                        title: '学历',
                        key: 'school'
                    },
                    {
                        title: '工作经验',
                        key: 'exp'
                    },
                    {
                        title: 'Action',
                        slot: 'action',
                        width: 150,
                        align: 'center'
                    },
                    {
                        title: '时间',
                        key: 'time'
                    },
                ],
                data6: [
                    {
                        name: 'John Brown',
                        sex:'男',
                        address: '1999.11',
                        school:'本科',
                        exp:'3年',
                        time:'2016.01.15 16:35',
                        skil:'java'
                    },
                    {
                        name: 'Jim Green',
                        sex: '女',
                        address: '1999.4',
                        school:'本科',
                        exp:'3年',
                        time:'2016.01.15 16:35',
                        skil:'web前端'
                    },
                    {
                        name: 'Joe Black',
                        sex: '男',
                        address: '2000.12',
                        school:'本科',
                        exp:'3年',
                        time:'2016.01.15 16:35',
                        skil:'HTML5'
                    },
                    {
                        name: 'Jon Snow',
                        sex: '女',
                        address: '2000.12',
                        school:'本科',
                        exp:'3年',
                        time:'2016.01.15 16:35',
                        skil:'JAVA大数据'
                    }
                ],
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: '简历信息',
                    content: `姓名：${this.data6[index].name}<br>性别：${this.data6[index].sex}<br>出生年月：${this.data6[index].address}<br>学历：${this.data6[index].school}<br>经验：${this.data6[index].exp}<br>技术掌握：${this.data6[index].skil}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            }
        }
    }
</script>