<template>
  <div>
    <div class="top-message">全国（包含中央企业在内的）招聘信息公开</div>
    <div class="wrapper" id="d-container">
      <div class="inner clearfix">
        <div class="infobox">
          <div class="article" frag="窗口3" portletmode="simpleArticleAttri">

            <div class="arti_title">南京美术馆2020年12月公开招聘高层次人才公告</div>
            <p class="arti_metas"><span class="arti_update">发布时间：2020-12-28</span></p>
            <div class="box01">
          <pre class="consent9">
        为适应南京美术馆现阶段工作和未来发展需要,经市委组织部核准，南京美术馆面向社会公开招聘2名编制内高层次人才，现将有关事项公告如下：

    一、招聘岗位

      本次招聘相关信息在南京党建、南京文艺生活等网站发布，具体岗位、数量、资格条件等详见《南京美术馆2020年12月公开招聘高层次人才岗位信息表》

    二、招聘条件

      （一）应聘人员应具备以下条件：

      1. 具有中华人民共和国国籍，享有公民的政治权利，有良好的政治思想素质，拥护党的路线方针政策；

      2. 热爱党的文艺事业，具有美术馆（或博物馆）相关工作经历，能胜任岗位职责；

      3. 身体健康，40周岁以下（1981年1月1日以后出生）；

      4. 符合岗位要求的其他资格条件（详见《岗位信息表》）。

      （二）有下列情形之一的，不得应聘：

      1.现役军人、普通高校在读非应届毕业生；

      2.尚未解除纪律处分或者正在接受纪律审查的人员、刑事处罚期限未满或者涉嫌违法犯罪正在接受调查的人员；

      3.与招聘单位负责人有夫妻关系、直系血亲关系、三代以内旁系血亲关系或者近姻亲关系、到岗后又有直接上下级领导关系的人员；

      4.国家和省另有规定不得应聘到事业单位有关岗位的人员。

    三、招聘程序

      （一）报名

      1. 报名时间：2020年12月29日起至2021年1月11日中午12时止。

      2. 报名方式：报名系统在线报名。

      3. 报名系统网址：http://47.92.104.188。

      4. 报名需上传材料：

      （1）报考人员在线填写基本信息表；

      （2）本人有效居民身份证（上传至“材料一”栏）；

      （3）各阶段（本科起）学历证书；

      （4）各阶段（本科起）学位证书；

      （5）《教育部学历证书电子注册备案表》电子照片（留学回国人员需提供国家教育部留学服务中心出具的《国外学历学位认证书》电子照片）；

      （6）工作经历证明电子照片（上传至“材料二”栏）；

      （7）能反映个人研究方向的主要业绩成果，包括组织策划的大型展览、重要学术期刊发表论文、出版著作、获得重要奖项等（填写至“奖惩情况”栏）。

      注1：工作经历证明指经单位盖章的“工作证明”、“劳动合同”或“单位社保缴纳记录证明”（三者选择其一即可）。

      注2：报考人员信息填写错漏、材料上传不全、材料模糊不清、不符合招聘条件均视为报名初审不合格。报名人员应对提交材料的真实性负责。

      （二）资格初审

      资格初审采用线上审核的方式。应聘人员可在报名后2个工作日于报名系统内查询审核状态，确认是否通过资格初审，请应聘人员登陆报名系统实时自查。

      （三）资格复审

      1. 资格初审合格人员进入资格复审。

      2. 资格复审采取现场审核的方式，须考生本人携相关材料参加审查。具体时间、地点以通知为准。

      3. 资格复审需携带材料：

      （1）《考生基本信息表》（于报名系统网站下载打印，手写签名）；

      （2）本人身份证原件及正反面复印件；

      （3）各阶段（本科起）学历证书原件及复印件；

      （4）各阶段（本科起）学位证书原件及复印件；

      （5）《教育部学历证书电子注册备案表》打印件（留学回国人员需提供国家教育部留学服务中心出具的《国外学历学位认证书》原件及复印件）；

      （6）工作经历证明原件及复印件；

      （7）能反映个人研究方向的主要业绩成果证明材料原件及复印件，包括组织策划的大型展览、重要学术期刊发表论文、出版著作、获得重要奖项等。

      4. 资格审查通过人员参加面试、综合考评。本次招聘不设开考比例，不收报名费和考试费。

      （四）面试、综合考评

      本次招聘高层次人才，采取面试和综合考评的方式，按照公开招聘有关规定和备案后的方案组织实施。

      1. 面试

      提前三天通知应聘人员面试相关事项。采取半结构化面试形式，成绩当场通知应聘人员。面试为百分制，成绩合格线为60分。

      2. 综合考评

      综合考评内容主要包括专业知识水平、学术成果、个人素质以及岗位匹配度等。综合考评为百分制，成绩合格线为60分。

      3. 总成绩计算

      按照面试占60%，综合考评占40%计算考核总成绩。

      总成绩及排名将于考评结束后3个工作日以内在南京文艺生活网站发布。

      （五）体检、考察

      总成绩合格人员，按岗位招聘数1：1的比例，从高分到低分确定体检人选。体检标准参照《公务员录用体检通用标准（试行）》。

      体检合格人员，由市文联对其思想政治表现、道德品质，以及与应聘岗位相关的业务能力和工作实绩等进行考察。考察工作参照《江苏省公务员录用考察办法》执行。

      （六）公示、聘用

        根据综合考评、体检和考察结果，经市文联党组研究决定拟聘用人员名单。拟聘用人员名单在南京党建、南京文艺生活等网站进行为期7个工作日的公示。公示内容包
      括岗位名称、拟聘用人员姓名、现工作或学习单位、招聘考试的各项成绩、总成绩、排名等。
        对公示无异议人员，经公开招聘综合管理部门备案后，由市文联为其办理有关聘用手续，并约定试用期。试用期满考核合格，予以定职定级；考核不合格，取消聘用资
      格，终止聘用关系。拟聘用人员应在规定时间内办理报到和有关手续，因个人原因逾期未办理相关手续的，取消其聘用资格。
        因体检或考察不合格、拟聘人选公示结果影响聘用、拟聘人选明确放弃聘用以及其他原因导致拟聘岗位空缺的，如需递补，可由市文联报公开招聘综合管理部门审核通
      过后，在该岗位的综合考评合格人员中，按总成绩从高分到低分依次递补。聘用审批后不再递补。

    四、相关待遇

      受聘人员享受事业单位在编专业技术人员工资待遇。

    五、招聘纪律

        应聘人员在报名、考试、体检、考察过程中或聘用后查实存在违纪违规行为的，按照《事业单位公开招聘违纪违规行为处理规定》（中华人民共和国人力资源和社会保
      障部第35号令），视具体情形和情节轻重，分别给予取消报考资格、取消考试成绩、不予聘用、记入诚信档案等处理，构成犯罪的依法追究刑事责任。

        招聘单位和招聘工作人员有违纪违规行为的，按照《事业单位公开招聘违纪违规行为处理规定》（中华人民共和国人力资源和社会保障部第35号令）进行处理。

    六、咨询监督

      （一）报名系统技术咨询电话：025-84687314（工作时间）

      （二）政策咨询电话：025－84578599（工作时间）

      （三）监督电话：025－84578551（工作时间）

      （四）受理时间：上午9:00－11:30 下午2:00－5:00

                                                                                              南京市文学艺术界联合会

                                                                                                  2020年12月28日


