<template>
  <div>
    <h2>企业基本信息</h2>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

      <el-form :inline="true" :model="ruleForm" :rules="rules" ref="ruleForma" label-width="100px" >
      <el-form-item label="公司网站：" prop="website" :inline="true" >
        <el-input v-model="ruleForm.website" ></el-input>
      </el-form-item>
      <el-form-item label="成立时间：" prop="establishment"  :inline="true" >
        <el-input v-model="ruleForm.establishment"></el-input>
      </el-form-item>
      </el-form>

      <el-form :inline="true" :model="ruleForm" :rules="rules" ref="ruleFormb" label-width="100px" >
        <el-form-item label="公司规模：" prop="scale"  >
          <el-input v-model="ruleForm.scale" ></el-input>
        </el-form-item>
        <el-form-item label="注册资金：" prop="registered"  >
          <el-input v-model="ruleForm.registered"></el-input>
        </el-form-item>
      </el-form>

      <el-form :inline="true" :model="ruleForm" :rules="rules" ref="ruleFormc" label-width="100px" >
        <el-form-item label="公司类型：" prop="companytype" >
          <el-select v-model="ruleForm.companytype" placeholder="请选公司类型">
            <el-option label="国企" value="guoqi"></el-option>
            <el-option label="民营" value="minying"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="法人代表" prop="legal"  >
          <el-input v-model="ruleForm.legal"></el-input>
        </el-form-item>
      </el-form>

      <el-form :inline="true" :model="ruleForm" :rules="rules" ref="ruleForms" label-width="100px" >
        <el-form-item label="公司电话：" prop="tel"  >
          <el-input v-model="ruleForm.tel" ></el-input>
        </el-form-item>
        <el-form-item label="联系人 ：" prop="name"  >
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
      </el-form>

      <el-form-item label="公司地址：" prop="address"  >
        <el-input v-model="ruleForm.address" placeholder="省/市/区/街道"></el-input>
      </el-form-item>

      <el-form-item label="办公环境：" prop="environment"  >
        <el-upload
            action="#"
            list-type="picture-card"
            :limit="3"
            :auto-upload="false">
          <i slot="default" class="el-icon-plus"></i>
          <div slot="file" slot-scope="{file}">
            <img
                class="el-upload-list__item-thumbnail"
                :src="file.url" alt=""
            >
            <span class="el-upload-list__item-actions">
        <span
            class="el-upload-list__item-preview"
            @click="handlePictureCardPreview(file)"
        >
          <i class="el-icon-zoom-in"></i>
        </span>
        <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleDownload(file)"
        >
          <i class="el-icon-download"></i>
        </span>
        <span
            v-if="!disabled"
            class="el-upload-list__item-delete"
            @click="handleRemove(file)"
        >
          <i class="el-icon-delete"></i>
        </span>
      </span>
          </div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>

      <el-form-item label="营业范围：" prop="scope">
        <el-input type="textarea" v-model="ruleForm.scope"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   @click="submitForm('ruleForm'),
                           submitForma('ruleForma'),
                           submitFormb('ruleFormb'),
                           submitFormc('ruleFormc'),
                           submitForms('ruleForms')">立即创建</el-button>
        <el-button @click="resetForm('ruleForm'),
                           resetForma('ruleForma'),
                           resetFormb('ruleFormb'),
                           resetFormc('ruleFormc'),
                           resetForms('ruleForms')" >重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
name: "companynote",
data() {
  return {
    ruleForm: {
      website: '',
      establishment: '',
      scale: '',
      registered: '',
      companytype:'',
      legal:'',
      tel:'',
      name:'',
      address:'',
      environment:'',
      scope: ''
    },
    rules: {
      website: [
        { required: true, message: '请输入公司网站', trigger: 'blur' },
      ],
      establishment: [
        { required: true, message: '请输入公司成立时间', trigger: 'blur' },
      ],
      scale: [
        { required: true, message: '请输入公司规模', trigger: 'blur' },
      ],
      registered: [
        { required: true, message: '请输入注册资金', trigger: 'blur' },
      ],
      companytype: [
        { required: true, message: '请选择公司类型', trigger: 'blur' },
      ],
      legal: [
        { required: true, message: '请输入法人代表', trigger: 'blur' },
      ],
      tel: [
        { required: true, message: '请输入公司电话', trigger: 'blur' },
      ],
      name: [
        { required: true, message: '请输入联系人姓名', trigger: 'blur' },
      ],
      address: [
        { required: true, message: '请输入公司地址', trigger: 'blur' },
      ],
      // environment: [
      //   { required: true, message: '请输至少上传三张图片', trigger: 'blur' },
      // ],
      scope: [
        { required: true, message: '请输入营业范围', trigger: 'blur' },
      ],
    },

    dialogImageUrl: '',
    dialogVisible: false,
    disabled: false
  };
},
methods: {
  submitForm(formName) {
    this.$refs[formName].validate((valid) => {
      if (valid) {
        console.log('submit!');
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  submitForma(formNamea) {
    this.$refs[formNamea].validate((valid) => {
      if (valid) {
        console.log('submit!');
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  submitFormb(formNameb) {
    this.$refs[formNameb].validate((valid) => {
      if (valid) {
        console.log('submit!');
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  submitFormc(formNamec) {
    this.$refs[formNamec].validate((valid) => {
      if (valid) {
        console.log('submit!');
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  submitForms(formNames) {
    this.$refs[formNames].validate((valid) => {
      if (valid) {
        console.log('submit!');
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  resetForm(formName) {
    this.$refs[formName].resetFields();
  },
  resetForma(formNamea) {
    this.$refs[formNamea].resetFields();
  },
  resetFormb(formNameb) {
    this.$refs[formNameb].resetFields();
  },
  resetFormc(formNamec) {
    this.$refs[formNamec].resetFields();
  },
  resetForms(formNames) {
    this.$refs[formNames].resetFields();
  },

  handleRemove(file) {
    console.log(file);
  },
  handlePictureCardPreview(file) {
    this.dialogImageUrl = file.url;
    this.dialogVisible = true;
  },
  handleDownload(file) {
    console.log(file);
  }
}
}
</script>

