<template>

    <div class="container">
        <h1>企业事记</h1>
        <el-button type="success" plain @click="handleAddClick">增加</el-button>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="number"
                    label="序号"
                    width="60">
            </el-table-column>
             <el-table-column
                    prop="date"
                    label="日期"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="title"
                    label="标题"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="messages"
                    width="400"
                    label="内容">
            </el-table-column>


            <el-table-column label="操作" width="177">
                <template slot-scope="scope">
                    <el-button type="primary" @click="handleEditClick(scope.$index,scope.row)"  size="mini">编辑</el-button>
                    <el-button type="danger" size="mini" @click="handleDelClick(scope.$index,scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="修改用户" :visible.sync="editBox" width="50%" :before-close="handleClose">
            <el-form ref="form" label-width="100px" v-model="user">
                <el-form-item label="序号:">
                    <el-input placeholder="请输入序号" maxlength="50" v-model = "user.number"></el-input>
                </el-form-item>
                <el-form-item label="日期:">
                    <el-input placeholder="请输入日期" maxlength="50" v-model = "user.date"></el-input>
                </el-form-item>
                <!--                <el-form-item label="时间:">-->
                <!--                    <el-date-picker-->
                <!--                            v-model = "user.date"-->
                <!--                            type="date"-->
                <!--                            placeholder="选择日期">-->
                <!--                    </el-date-picker>-->
                <!--                </el-form-item>-->
                <el-form-item label="标题:">
                    <el-input placeholder="请输入标题" maxlength="50" v-model = "user.title"></el-input>
                </el-form-item>
                <el-form-item label="内容:">
                    <el-input placeholder="请输入内容" maxlength="50" v-model = "user.messages"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleEditUser">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="添加用户" :visible.sync="addBox" width="50%" :before-close="handleClose">
            <el-form ref="form" label-width="100px" v-model="addUserData">
                <el-form-item label="序号:">
                    <el-input placeholder="请输入序号" maxlength="50" v-model = "addUserData.number"></el-input>
                </el-form-item>
                <el-form-item label="时间:">
                    <el-date-picker
                            v-model = "addUserData.date"
                            type="date"
                            placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="标题:">
                    <el-input placeholder="请输入标题" maxlength="50" v-model = "addUserData.title"></el-input>
                </el-form-item>
                <el-form-item label="内容:">
                    <el-input placeholder="请输入内容" maxlength="50" v-model = "addUserData.messages"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleAddUser">确 定</el-button>
            </span>
        </el-dialog>

    </div>
</template>


<script>
    export default {
        name: "addQiye",
        data() {
            return {
                tableData: [{
                    number:'1',
                    date: '2016-03-02',
                    title: '张三',
                    messages: '广州市天河区金沙江路 1518 弄'
                }, {
                  number:'',
                    date: '2016-03-02',
                    title: '张三',
                    messages: '广州市天河区金沙江路 1518 弄'
                }, {
                    number:'',
                    date: '2016-03-02',
                    title: '张三',
                    messages: '广州市天河区金沙江路 1518 弄'
                }, {
                    number:'',
                    date: '2016-03-02',
                    title: '张三',
                    messages: '广州市天河区金沙江路 1518 弄'
                },{
                  number:'',
                  date: '2016-03-02',
                  title: '张三',
                  messages: '广州市天河区金沙江路 1518 弄'
                },{
                  number:'',
                  date: '2016-03-02',
                  title: '张三',
                  messages: '广州市天河区金沙江路 1518 弄'
                }],
                addBox : false,
                editBox:false,
                user:{},
                editIndex:"",
                addUserData:{
                    number:'',
                    date : "",
                    title : "",
                    message : ""
                }
            }
        },
        methods:{
            handleEditClick(index,row){
                this.editBox = true
                this.user = row
                this.editIndex = index
            },

            // eslint-disable-next-line no-unused-vars
            handleDelClick(index,row){
                this.$confirm('此操作将删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.tableData.splice(index,1)
                    this.$message({showClose: true, message: "删除成功", type: 'success'});
                })
            },
            handleClose(done) {
                done();
            },
            handleEditUser(){
                this.tableData.splice(this.editIndex,1,this.user)
                this.$message({showClose: true, message: "修改成功", type: 'success'});
                this.editBox = false
            },
            handleAddClick(){
                this.addBox = true
            },
            handleAddUser(){
                let strDate = dateFormat("YYYY-mm-dd",this.addUserData.date)
                this.addUserData.date = strDate
                this.tableData.push(this.addUserData)
                this.addBox = false
                this.addUserData = {}
                this.$message({showClose: true, message: "添加成功", type: 'success'});
            }
        }
    }

    function dateFormat(fmt, date) {
        let ret;
        const opt = {
            "Y+": date.getFullYear().toString(),        // 年
            "m+": (date.getMonth() + 1).toString(),     // 月
            "d+": date.getDate().toString(),            // 日
            "H+": date.getHours().toString(),           // 时
            "M+": date.getMinutes().toString(),         // 分
            "S+": date.getSeconds().toString()          // 秒
            // 有其他格式化字符需求可以继续添加，必须转化成字符串
        };
        for (let k in opt) {
            ret = new RegExp("(" + k + ")").exec(fmt);
            if (ret) {
                fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
            }
        }
        return fmt;

    }
</script>

