<template>
  <div>
    <div style="width: 1300px; margin: 0 auto">
      <a-descriptions title="xxx简历" bordered>
        <a-descriptions-item label="姓名" v-model="name">{{name}}</a-descriptions-item>
        <a-descriptions-item label="身份证号码" :span="2" v-model="ID">
          {{ ID }}
        </a-descriptions-item>
        <a-descriptions-item label="标题" v-model="title"> {{title}} </a-descriptions-item>
        <a-descriptions-item label="出生日期" v-model="birthday"> {{ birthday }} </a-descriptions-item>
        <a-descriptions-item label="邮箱" :span="2" v-model="email">
          {{ email }}
        </a-descriptions-item>
        <a-descriptions-item label="手机号码" v-model="tel"> {{ tel }}</a-descriptions-item>
        <a-descriptions-item label="现居住地" v-model="nowplace"> {{nowplace}} </a-descriptions-item>

        <a-descriptions-item label="婚姻状况" v-model="marry"> {{marry}} </a-descriptions-item>
        <a-descriptions-item label="最高学历" v-model="highestEdu"> {{highestEdu}} </a-descriptions-item>
        <a-descriptions-item label="所学专业" v-model="major">
          {{major}}
        </a-descriptions-item>
        <a-descriptions-item label="身高" v-model="height"> {{height}} </a-descriptions-item>
        <a-descriptions-item label="健康情况" v-model="health"> {{health}} </a-descriptions-item>
        <a-descriptions-item label="民族" v-model="nation">{{nation}} </a-descriptions-item>
        <a-descriptions-item label="体重" v-model="weight"> {{weight}} </a-descriptions-item>
        <a-descriptions-item label="政治面貌" :span="1" v-model="political">
          {{political}}
        </a-descriptions-item>
        <a-descriptions-item label="简历树" :span="1">
          <template>
            <div>
              <a-button type="primary" @click="showModal">
                点击打开简历树
              </a-button>
              <a-modal
                  title="简历树"
                  :visible="visible2"
                  :confirm-loading="confirmLoading"
                  @ok="handleOk"
                  @cancel="handleCancel"
                  style="width:550px"
              >
                <template>
                  <div class="tree">
                    <a-timeline mode="alternate">
                      <a-timeline-item v-for="(tree) in trees" :key="tree.experience">{{tree.experience}}</a-timeline-item>
                      <a-timeline-item>
                        <a-icon slot="dot" type="clock-circle-o" style="font-size: 16px;" />
                        投递简历
                      </a-timeline-item>
                    </a-timeline>
                  </div>
                </template>
              </a-modal>
            </div>
          </template>
        </a-descriptions-item>
        <a-descriptions-item label="兴趣爱好" :span="3" v-model="hobby">
          {{hobby}}
        </a-descriptions-item>
      </a-descriptions>
      <template>
        <a-table :columns="columns" :data-source="dataSource" bordered>
          <template
              v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
              :slot="col"
              slot-scope="text, record"
          >
            <div :key="col">
              <a-input
                  v-if="record.editable"
                  style="margin: -5px 0"
                  :value="text"
                  @change="(e) => handleChange(e.target.value, record.key, col)"
              />
              <template v-else>
                {{ text }}
              </template>
            </div>
          </template>
        </a-table>
      </template>
      <template>
        <a-table :columns="columns2" :data-source="dataSource" bordered>
          <template
              v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
              :slot="col"
              slot-scope="text, record"
          >
            <div :key="col">
              <a-input
                  v-if="record.editable"
                  style="margin: -5px 0"
                  :value="text"
                  @change="(e) => handleChange(e.target.value, record.key, col)"
              />
              <template v-else>
                {{ text }}
              </template>
            </div>
          </template>
        </a-table>
      </template>
      <template>
        <a-table :columns="columns3" :data-source="dataSource" bordered>
          <template
              v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
              :slot="col"
              slot-scope="text, record"
          >
            <div :key="col">
              <a-input
                  v-if="record.editable"
                  style="margin: -5px 0"
                  :value="text"
                  @change="(e) => handleChange(e.target.value, record.key, col)"
              />
              <template v-else>
                {{ text }}
              </template>
            </div>
          </template>
        </a-table>
      </template>
    </div>
  </div>
</template>

<script>

export default{
  components: {
  },
  data () {
    return {
      ModalText: 'Content of the modal',
      confirmLoading: false,
      form: this.$form.createForm(this),
      visible: false,
      visible2: false,
      value: 3,
      desc: ['terrible', 'bad', 'normal', 'good', 'wonderful'],
      name: 'XXX',
      ID:'411520200002111102',
      title: 'XXXXX',
      money:'12×30K',
      email:'111111111@qq.com',
      birthday:'2020-1-1',
      state:'技术岗位',
      tel:'10101010101',
      place:'上海',
      nowplace:'郑州',
      marry:'已婚',
      highestEdu:'硕士',
      major:'计算机科学与技术',
      height:'178',
      health:'优',
      nation:'汉',
      weight:'130',
      political:'共青团员',
      hobby:'运动',
      editingKey: '',
      count: 2,
      columns: [
        {
          title: '学历类型',
          dataIndex: 'studyStyle',
          scopedSlots: {customRender: 'studyStyle'},
        },
        {
          title: '开始日期',
          dataIndex: 'beginDate',
          scopedSlots: {customRender: 'beginDate'},
        },
        {
          title: '结束日期',
          dataIndex: 'endDate',
          scopedSlots: {customRender: 'endDate'},
        },
        {
          title: '学校名称',
          dataIndex: 'schoolName',
          scopedSlots: {customRender: 'schoolName'},
