        },
        {
          title: '备注',
          dataIndex: 'remarked',
          scopedSlots: {customRender: 'remarked'},
        },
      ],
      columns2: [
        {
          title: '工作类型',
          dataIndex: 'workStyle',
          scopedSlots: { customRender: 'workStyle' },
        },
        {
          title: '开始日期',
          dataIndex: 'beginDate',
          scopedSlots: { customRender: 'beginDate' },
        },
        {
          title: '结束日期',
          dataIndex: 'endDate',
          scopedSlots: { customRender: 'endDate' },
        },
        {
          title: '工作单位',
          dataIndex: 'workPlace',
          scopedSlots: { customRender: 'workPlace' },
        },
        {
          title: '工作岗位',
          dataIndex: 'major',
          scopedSlots: { customRender: 'major' },
        },
        {
          title: '职称',
          dataIndex: 'workName',
          scopedSlots: { customRender: 'workName' },
        },
        {
          title: '获奖证书',
          dataIndex: 'praise',
          scopedSlots: { customRender: 'praise' },
        },
        {
          title: '证明人',
          dataIndex: 'prove',
          scopedSlots: { customRender: 'prove' },
        },
        {
          title: '联系电话',
          dataIndex: 'tel',
          scopedSlots: { customRender: 'tel' },
        },
      ],
      columns3:[
        {
          title: '状态',
          dataIndex: 'state',
        },
        {
          title: '技能名称',
          dataIndex: 'skillName',
        },
        {
          title: '使用日期',
