          dataIndex: 'useMouth',
        },
        {
          title: '熟练程度',
          dataIndex: 'content',
        },
        {
          title: '备注',
          dataIndex: 'remarked',
        },
      ],
      trees:[
        {experience: '河南师范大学本科毕业 2022-06-30'},
        {experience: '清华大学研究生毕业 2025-06-30'},
        {experience: '中科院软件研究院2025-07-1'},
        {experience: '阿里达摩院 2030-07-01'},
      ],
    };
  },
  methods: {
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    showModal() {
      this.visible2 = true;
    },
    handleOk() {
      this.ModalText = 'The modal will be closed after two seconds';
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible2 = false;
        this.confirmLoading = false;
      }, 2000);
    },
    handleCancel() {
      console.log('Clicked cancel button');
      this.visible2 = false;
    },
  },
}
</script>
<style>
.tree{
  margin-top:50px;
}
</style>