<template>
    <div class="boss">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="订单号：" prop="name">
                <el-input v-model="ruleForm.name" width="400px"></el-input>
            </el-form-item>
            <el-form-item label="充值金额：" prop="jine">
                <el-input v-model="ruleForm.jine"></el-input>
            </el-form-item>
            <el-form-item label="支付方式" prop="resource">
                <el-radio-group v-model="ruleForm.resource">
                  <div class="one" @click="yincang()" style="float:left"><el-radio label="支付宝"></el-radio></div>
                  <div class="one" @click="dayin()" style="float:left"><el-radio label="网上银行"></el-radio></div>
                </el-radio-group>
            </el-form-item>
            <div id="yincang" ref="chazhao">
                <el-form-item label="选择银行" prop="type">
                  <!-- <el-checkbox-group v-model="ruleForm.type"> -->
                  <el-radio-group v-model="ruleForm.type">
                    <el-radio label="建设银行" name="type"></el-radio>
                    <el-radio label="农业银行" name="type"></el-radio>
                    <el-radio label="开发银行" name="type"></el-radio>
                    <el-radio label="邮政银行" name="type"></el-radio>
                  </el-radio-group>
                </el-form-item>
            </div>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        ruleForm: {
          name: '',
          jine: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入订单号', trigger: 'blur' }
          ],
          jine: [
            {required:true, message: '请输入充值金额', trigger:'blur'}
          ],
          type: [
            { required: true, message: '请选择银行种类', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择支付方式', trigger: 'change' }
          ],
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.$refs.chazhao.style.display="none";
      },
      dayin() {
          this.$refs.chazhao.style.display="block";
      },
      yincang() {
        this.$refs.chazhao.style.display="none";
      },
    }
  }
</script>
<style scoped>
    .boss{
        margin:60px 410px;
        width: 60%;
    }
    #yincang{
      display: none;
    }
    . el-form-item__content{
      margin-left: 200px !important;
    }
    .one{
      width: 100px;
      display: block !important;
      height: 20px;
      margin-left: 110px;
    }
    .el-form-item {
    margin-bottom: 22px;
    width: 400px;
}
</style>
