<template>
  <div>
    <h2>招聘账号管理</h2>

    <el-row :gutter="30">

      <el-col :span="8" >
        <div>
          <el-input placeholder="请输入名字" clearable v-model="search" >
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
      </el-col>

      <el-col :span="16"  >

        <div>
          <el-button type="primary" @click="dialogVisible=true; needadd();">添加</el-button>

          <el-dialog title="新增员工账号" :visible.sync="dialogVisible" width="30%">
            <el-form ref="form" :model="form" label-width="100px" :label-position="right">
              <el-form-item label="姓名：">
                <el-input v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="员工号：">
                <el-input v-model="form.order"></el-input>
              </el-form-item>
              <el-form-item label="电话号码：">
                <el-input v-model="form.tel"></el-input>
              </el-form-item>
              <el-form-item label="角色：">
                <el-input v-model="form.role"></el-input>
              </el-form-item>
              <el-form-item label="岗位：">
                <el-input v-model="form.post"></el-input>
              </el-form-item>
              <el-form-item label="所属部门：">
                <el-input v-model="form.department"></el-input>
              </el-form-item>
              <el-form-item label="主管领导：">
                <el-input v-model="form.leader"></el-input>
              </el-form-item>
              <el-form-item label="负责区域：">
                <el-input v-model="form.area"></el-input>
              </el-form-item>
              <el-form-item label="岗位描述：">
                <el-input v-model="form.description"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false;sumbitaddRow();">保 存</el-button>
      </span>
          </el-dialog>
        </div>

      </el-col>

    </el-row>

    <el-table
        :data="tables"
        style="width: 100%"
        class="el-table-re">

      <el-table-column
          type="index"
          label="序号 "
          width="50">
      </el-table-column>

      <el-table-column
          label="状态 "
          width="120">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.state">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
          label="账号"
          prop="order"
          width="120">
      </el-table-column>
      <el-table-column
          label="员工姓名"
          prop="name"
          width="120">
      </el-table-column>
      <el-table-column
          label="联系电话"
          prop="tel"
          width="120">
      </el-table-column>
      <el-table-column
          label="角色"
          prop="role"
          width="120">
      </el-table-column>
      <el-table-column
          label="岗位"
          prop="post"
          width="120">
      </el-table-column>
      <el-table-column
          label="所属部门"
          prop="department"
          width="120">
      </el-table-column>
      <el-table-column
          label="创建时间"
          prop="start"
          width="120">
      </el-table-column>
      <el-table-column
          label="主管领导"
          prop="leader"
          width="120">
      </el-table-column>
      <el-table-column
          label="岗位描述"
          prop="description">
      </el-table-column>
      <el-table-column
          label="负责领域"
          prop="area"
          width="120">
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">修改</el-button>
          <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :visible.sync="centerDialogVisible">

      <el-form ref="form" :model="editForm" label-width="100px">
        <el-form-item label="账号：">
          <el-input v-model="editForm.order"></el-input>
        </el-form-item>
        <el-form-item label="员工姓名：">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="联系电话：">
          <el-input v-model="editForm.tel"></el-input>
        </el-form-item>
        <el-form-item label="角色：">
          <el-input v-model="editForm.role"></el-input>
        </el-form-item>
        <el-form-item label="岗位：">
          <el-input v-model="editForm.post"></el-input>
        </el-form-item>
        <el-form-item label="所属部门：">
          <el-input v-model="editForm.department"></el-input>
        </el-form-item>
        <el-form-item label="主管领导：">
          <el-input v-model="editForm.leader"></el-input>
        </el-form-item>
        <el-form-item label="岗位描述：">
          <el-input v-model="editForm.description"></el-input>
        </el-form-item>
        <el-form-item label="负责区域：">
          <el-input v-model="editForm.area"></el-input>
        </el-form-item>
      </el-form>

      <div>
        <el-button @click="centerDialogVisible=false">取消</el-button>
        <el-button type="primary" @click="sumbitEditRow()">确定</el-button>
      </div>
    </el-dialog>

    <el-button-group>
      <el-button type="primary" icon="el-icon-arrow-left"  size="mini">上一页</el-button>
      <el-button type="primary"  size="mini">下一页<i class="el-icon-arrow-right el-icon--right" ></i></el-button>
    </el-button-group>

  </div>
</template>

<script>
let _index;
let a_index;
export default {
  name: "recruitment",
  data() {
    return {
      editForm:[],
      tableData: [{
        state:true,
        order:'1601150930',
        name: '王大二',
        tel:'13833725790',
        role:'操作员',
        post:'java工程师',
        department:'工程部',
        start:'2020-12-30',
        leader:'王二',
        description:'岗位的描述话语',
        area:'java开发',
      }, {
        state:false,
        order:'1601150930',
        name: '王小二',
        tel:'13833725790',
        role:'操作员',
        post:'java工程师',
        department:'工程部',
        start:'2020-12-30',
        leader:'王二',
        description:'岗位的描述话语',
        area:'java开发',
      }, {
        state:false,
        order:'1601150930',
        name: '莉丝',
        tel:'13833725790',
        role:'操作员',
        post:'java工程师',
        department:'工程部',
        start:'2020-12-30',
        leader:'王二',
        description:'岗位的描述话语',
        area:'java开发',
      }, {
        state:true,
        order:'1601150930',
        name: '李晓晓',
        tel:'13833725790',
        role:'操作员',
        post:'java工程师',
        department:'工程部',
        start:'2020-12-30',
        leader:'王二',
        description:'岗位的描述话语',
        area:'java开发',
      }],
      dialogVisible:false,
      centerDialogVisible:false,
      form: {
        state:true,
        order:'',
        name: '',
        leader:'',
        description:'',
        department:'',
        start:(new Date().getFullYear()+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate()),
        role:'',
        post:'',
        area:'',
      },
      search: '',
    }
  },
  computed: {
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(data => {
          return Object.keys(data).some(key => {
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  },
  methods: {
    handleEdit(index, row) {
      // console.log(index, row);
      this.centerDialogVisible = true
      this.editForm = Object.assign({}, row);//重置对象
      _index = index;
    },
    needadd(){
      this.tableData.push(this.form);
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    sumbitEditRow() {
      const editData = _index;
      console.log("editData的值"+	this.editForm)
      this.tableData[editData].order = this.editForm.order;
      this.tableData[editData].name = this.editForm.name;
      this.tableData[editData].tel = this.editForm.tel;
      this.tableData[editData].role = this.editForm.role;
      this.tableData[editData].post = this.editForm.post;
      this.tableData[editData].department = this.editForm.department;
      this.tableData[editData].leader = this.editForm.leader;
      this.tableData[editData].description = this.editForm.description;
      this.tableData[editData].area = this.editForm.area;
      this.centerDialogVisible = false;
      console.log("数据："+this.editForm.date)
      console.log("对象数组",this.tableData)
    },
    sumbitaddRow() {
      const addData = a_index;
      console.log("addData的值"+	this.Form)
      this.tableData[addData].name = this.Form.name;
      this.tableData[addData].order = this.Form.order;
      this.tableData[addData].tel = this.Form.tel;
      this.tableData[addData].department = this.Form.department;
      this.tableData[addData].role = this.Form.role;
      this.tableData[addData].post = this.Form.post;
      this.tableData[addData].area = this.Form.area;
      this.tableData[addData].leader = this.Form.leader;
      this.tableData[addData].description = this.Form.description;
      this.dialogVisible = false;
      console.log("数据："+this.Form.date)
      console.log("对象数组",this.tableData)
    },
  }
}

</script>

<style scoped>
.el-row{
  margin-top: 15px;
}
.el-table-re{
  margin-top: 15px;
}
.el-button-group{
  margin-top: 15px;
}
</style>