<template>
    <div class="boss">
        <form>
            <h2>新增大事记</h2>
            <table>
                <tr>
                    <td class="one1">日期：</td>
                    <td><el-date-picker
                            v-model="value1"
                            type="date"
                            placeholder="选择日期">
                        </el-date-picker></td>
                    <!-- <td><input type="text"></td> -->
                </tr>
                <tr>
                    <td class="one1">标题：</td>
                    <td><input type="text"></td>
                </tr>
                <tr>
                    <td class="one1">内容：</td>
                    <td><input type="text" style="margin-bottom:20px; width:300px;height:150px;" ></td>
                </tr>
            </table>
                <button style="margin-right:20px;">确定</button>
                <button style="margin-left:20px;">返回</button>
        </form>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value1: '',
        value2: '',
      };
    }
  };
</script>

<style scoped>
.boss{
    text-align: center;
    width: 100%;
    height: 100%;
    /* background-color:#f1f1f1; */
    padding-top: 60px;
    padding-bottom: 100px;
}
table{
    margin: 0 auto;
    border-radius: 4px;
    box-shadow: 0 0 8px rgba(0,0,0,.1);
    background-color: #fff;
}
.one1{
    display: inline;
    width: 100px;
    height: 40px;
    text-align: center;
}
input{
    height: 40px;
    width: 220px;
    padding: 4px 12px 4px 12px;
    border: 1px solid #c8c8c8;
    background-color: hsla(0,0%,71%,.1);
    border-radius: 4px;
    outline: none;
    margin-right:20px;
}
td{
    float: left;
    margin-top: 20px;
}
button{
    margin-top:20px;
    width: 100px;
    height: 30px;
    border-radius: 20px;
    background-color: #187cb7;
    border-style: none;
    color: #fff;
    font-size: 18px;
    outline: none;
}
</style>