          <div class="article" frag="窗口3" portletmode="simpleArticleAttri">

            <div class="arti_title">2020年宁波市奉化区教育系统招聘紧缺岗位事业编制教师公告</div>
            <p class="arti_metas"><span class="arti_update">发布时间：2020-12-24</span></p>
            <div class="box01">
          <pre class="consent9">
      为满足我区普通高中学校学生高考选考科目报名学习需要，根据宁波市教育局《关于宁波市教育系统事业单位公开招聘工作人员的实施意见》(甬教人〔2008〕12号〕
    有关规定，决定面向浙江省公开招聘紧缺岗位事业编制教师5名。现就有关事项公告如下：

    一、招聘原则和办法

      招聘工作坚持公开、平等、竞争、择优原则，按照德才兼备的用人标准，采取公开报名、统一考试和择优聘用的办法进行。

    二、招聘对象和条件

      应聘人员应具备下列规定的基本条件和资格条件:

      (一)遵守中华人民共和国宪法、法律和法规;

      (二)遵守纪律、品行端正，具备良好的职业素质;

      (三)浙江省户籍，年龄35周岁以下(1984年12月30日以后出生);具有硕士研究生以上学历学位或副高级以上专业技术职务任职资格的,年龄可放宽至45周岁以下
      (1974年12月30日以后出生);

      (四)具有适应岗位要求的身体条件;

      (五)具体岗位及条件详见附件1。

    三、招聘程序

      (一)报名

      1.报名：应聘人员在仔细阅读公告信息后，需按要求填写个人信息，并将报名表、专业技术证书、地市级荣誉证书和学术成果等资料扫描件以电子邮件形式发送到报名
            邮箱：67126723@qq.com，接受资格初审。

      2.报名时间：2020年12月30日-2021年1月18日，逾期不再接受报名。

      3.符合条件的报名人数和招聘计划数之比不足3:1时，按实际报名人数参加考试。

      4.由区教育局负责通知资格审查通过人员，并同时告知考试时间和地点，资格审查未通过人员则以短信形式告知。通过资格审查人员未按通知时间到达指定地点参加考
            试，视作自动放弃。

      (二)考试

      本次公开招聘考试分为初试和复试两个环节。同一岗位报名人数与招聘计划数之比大于3:1时增加初试环节;同一岗位报名人数与招聘计划数之比小于等于3:1时直接进入
            复试。

      1.初试

      初试采用结构化面试方式，主要考察应聘人员的问题分析能力、语言表达能力、举止仪表等综合素养。总分100分，不足70分者予以淘汰。初试成绩不计入总成绩。

      初试结束后，根据初试成绩从高分到低分，按招聘计划数3倍确定复试人员，不足比例的按实际人数确定。初试成绩和复试人员名单现场公布。

      2.复试

      复试采取试讲的形式，主要考察应聘人员的课堂教学能力、专业知识、综合知识。总分为100分，不足70分者予以淘汰。复试成绩即最终成绩。

      复试结束后，根据复试成绩从高分到低分按招聘计划数1:1比例确定体检对象，若复试成绩相同，则采取现场加试方式确定体检对象。

      (三)体检

        体检对象参加由宁波市奉化区教育局统一组织的体检，参加体检人员名单在宁波市奉化区人力资源和社会保障局和宁波市奉化区教育局门户网站公布。体检具体时间及
      相关事项另行通知。体检按《公务员录用体检通用标准》等公务员录用体检相关文件规定执行。不按规定的时间、地点参加体检或体检结果不合格者淘汰。

      (四)考察

      体检合格者进入考察。考察参照《关于做好公务员录用考察工作的通知》(国公局发〔2013〕2号)等公务员录用考察相关文件规定执行。考察不合格者淘汰。

      (五)递补规定

        如体检对象放弃体检或体检不合格，考察对象放弃考察或考察不合格，用人单位认为有必要在其余面试人选中作递补的，可按程序报区人力资源和社会保障局核准，在
      该岗位复试合格人员中按复试成绩从高分到低分予以递补。

      (六)公示、聘用

      1.考察合格后，对拟聘用人员进行公示，公示期为7个工作日。公示期满后无异议的，按规定程序办理聘用手续,签订聘用合同。新聘用人员按有关规定实行三个月试用期
        试用期满不能胜任教育教学工作，考核不合格的，解除聘用合同。新聘人员工资福利待遇按有关规定执行。

      2.拟聘用人员现在有工作单位的，须按有关规定办理好与原单位终止劳动人事关系手续后予以聘用。拟聘用人员应在规定时间内报到，无正当理由逾期不报到的，取消聘
        用资格。在办理人事关系转移手续时仍将审核档案资料，若发现拟聘用人员档案资料有不符合报考条件的，将取消聘用资格。

    四、其他事项

      1.此次拟公开招聘紧缺岗位事业编制教师的学校为宁波市奉化区教育局直属事业单位(普通高中学校)。

      2.新聘人员享受奉化区人才引进政策和区教育局名优教师管理办法规定的相关待遇。

      3.联系电话(工作时间)：0574-88521521，88510721。监督电话：88523822。

      宁波市奉化区人力资源和社会保障局

      宁波市奉化区教育局

      2020年12月24日






