var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('div',{staticClass:"top-message"},[_vm._v("全国（包含中央企业在内的）招聘信息公开")]),_c('div',{staticClass:"wrapper",attrs:{"id":"d-container"}},[_c('div',{staticClass:"inner clearfix"},[_c('div',{staticClass:"infobox"},[_c('div',{staticClass:"article",attrs:{"frag":"窗口3","portletmode":"simpleArticleAttri"}},[_c('div',{staticClass:"arti_title"},[_vm._v("中国南方电网有限责任公司2021年校园招聘公告")]),_c('p',{staticClass:"arti_metas"},[_c('span',{staticClass:"arti_update"},[_vm._v("发布时间：2020-01-06")])]),_c('div',{staticClass:"box01"},[_c('pre',{staticClass:"consent9"},[_vm._v("  中国南方电网有限责任公司（以下简称我公司） 2021年校园招聘工作于2021年01月06日正式启动。现将有关事项公告如下：\n\n  一，南方电网公司简介\n\n     根据国务院《电力体制改革方案》，中国南方电网公司于2002年12月29日正式挂牌成立。公司属中央管理，由国务院国资委扩大出资人事。南方电网覆盖五省区，并与\n  香港，澳门地区以及东南亚国家的电网相联，提供面积100万平方公里，拥有人口2.5 4亿人，为客户提供9270万户。南方电网是国内率先“走出去”的电网。作为国务院确\n  定的大湄公河次区域电力合作中方执行单位，公司积极落实“一带一路”倡议，加强与大湄公河次区域国家，港澳地区的电力合作。公司连续1 4年在国务院国资委经营业绩\n  考核中位列A级；连续1年6年入围全球500强；公司坚守绿色发展理念，着力建立安全，可靠，绿色，高效的智能电网，推动生态文明建设。企业，目前列第105位。\n\n  二，招聘单位\n\n    公司2021年校园招聘工作，由公司下属的超高压输电公司，广东电网公司，广西电网公司，云南电网公司，贵州电网公司，海南电网公司，深圳供电局有限公司，调峰调\n  频发电公司，南方电网产业投资集团有限公司，南方电网资本控股公司，南方电网财务有限公司（南方电网国际金融有限公司），鼎状语从句：财产保险股份有限公司，南\n  方电网国际有限公司，南方电网云南国际有限公司，南方电网科学研究院，南方电网能源发展研究院，南方电网数字电网研究院，南方电网物资有限公司，南方电网公司培\n  训与评价中心（南网党校，南网领导力学院），南方电网公司北京分公司等20家分子公司和南方电网电力调度控制中心作为招聘单位具体组织实施。\n\n  三，招聘工作安排\n\n    公司2021年校园招聘工作主要分为信息发布，简历筛选，考核测评，审核录用，公示等5个主要方面。详情可登录我公司员工招聘系统（网址zhaopin.csg.cn ），了解\n  各个单位的具体招聘信息。\n\n  四，应聘注意事项\n\n  1.应聘者须通过公司招聘系统填报简历，上传有关材料（如成绩单，学籍认证报告，应聘者推荐表，学历学位证书，相关技术及技能等级证书等扫描件），申请志愿，并保\n  证所填信息真实，完整，准确。属于必填项的相关信息，应聘者必须填写，不得漏填。\n\n  2.每个应聘者最多替代公司所属的3家分子公司（即3家二级单位）申请志愿，每家分子公司替代的下属单位数量和职位数量详见各分子公司的校园招聘公告要求。应聘单位\n  和职位志愿一旦提交，将不能修改，请广大应聘者认真思考，慎重填报。\n\n  3.应聘者对个人填报的应聘信息的真实性，准确性和正确负责。对于存在虚假，伪造等不实信息的，一经发现将取消应聘者的应聘资格。\n\n  4.为保证及时收到招聘代理商的相关通知信息，请应聘者及时登录公司招聘系统查看，并根据要求确认回复。同时，请保持注册的手机，邮箱等联系方式的畅通。\n\n  5.详细的注意事项及相关问题，请阅读公司员工招聘系统首页“应聘指南”栏目下的“应聘须知”和“常见问题”。\n\n  五，其他\n\n  1.南方电网公司员工招聘系统（zhaopin.csg.cn ）是我公司及所属各单位发布20 21年校园招聘信息，简历接收状语从句：投递志愿的唯一官方网站。我公司官方微信公\n  众号“南网50Hz的”（微信号csg50Hz ）授权转发相关招聘信息。国务院国资委网站“中央企业校园招聘”栏目，人力资源和社会保障部网站转发我公司招聘链接。此外，未\n  授权其他网站，机构或个人对发布虚假招聘信息的个人或机构，我公司保留追究法律责任的权利。\n\n  2.“南网微招聘” APP是我公司员工招聘的官方移动应用，下载链接见公告下方的二维码。\n\n  3.我公司所属各单位在校园招聘过程中，均不支付任何费用。任何针对我公司及所属单位的招聘辅导班，考试大纲，复习资料等，均与我公司及所属单位无关。\n\n\n\n\n\n\n\n                                                                                  中国南方电网有限责任公司人力资源部\n\n                                                                                             2021年01月06日\n        ")])]),_c('div',{staticClass:"entry"})])])])])])}]

