<template>
  <div>
  <div class="top">
    <div class="top1"><img :src="img1" alt=""><p><span>Hot</span>手机app</p></div>
    <div class="top2"><img :src="img2" alt=""><p><a-icon type="wechat" style="color: green;margin-right: 3px;font-size:16px" />微信公众号</p></div>
    <div class="top3">
      <ul>
        <li><a-icon style="font-size: 30px" type="qq" /></li>
        <li>意见反馈</li>
        <li>安全宣传</li>
        <li class="goTop" v-show="goTopShow" @click="goTop">
          <a-icon style="font-size: 30px" type="up" />
        </li>

      </ul>
    </div>
  </div>

  </div>
</template>
<script>
import img1 from '@/assets/phone_app.gif'
import img2 from '@/assets/weixin_03.gif'
export default {
  name: "goTop",
  components:{
  },
  data() {
    return {
      scrollTop: "",
      goTopShow: false,
      img1,
      img2
    };
  },
  watch: {
    scrollTop() {
      if (this.scrollTop > 100) {
        this.goTopShow = true;
      } else {
        this.goTopShow = false;
      }
    }
  },
  methods: {
    handleScroll() {
      this.scrollTop =
          window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if (this.scrollTop > 200) {
        this.goTopShow = true;
      }
    },
    goTop() {
      let timer = null,
          _that = this;
      cancelAnimationFrame(timer);
      timer = requestAnimationFrame(function fn() {
        if (_that.scrollTop > 0) {
          _that.scrollTop = _that.scrollTop - _that.scrollTop/25;
          document.body.scrollTop = document.documentElement.scrollTop = _that.scrollTop;
          timer = requestAnimationFrame(fn);
        } else {
          cancelAnimationFrame(timer);
          _that.goTopShow = false;
        }
      });
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  }
};
</script>

<style scoped>
.goTop {

}
.goTop:hover .goTopIcon {
