import foot from "@/components/index/foot";
import school from "@/components/index/school";
import company from "@/components/index/company";
import Header from "@/components/index/Header"
import headmain from "@/components/index/headmain"
import search from "@/components/index/search";
import scrollTop from "@/components/index/scrollTop";
foot.install = function(Vue) {
    Vue.component('foot', foot);
    Vue.component('company', company);
    Vue.component('school', school);
    Vue.component('Header', Header);
    Vue.component('headmain', headmain);
    Vue.component('search', search);
    Vue.component('scrollTop',scrollTop)
};
export default foot;company;school;Header;headmain;search;scrollTop;