<template>
  <div class="button">
    <a-button type="primary" @click="showModal">
      增加
    </a-button>
    <a-modal
        title="增加专业技能"
        :visible="visible"
        :confirm-loading="confirmLoading"
        @ok="handleOk"
        @cancel="handleCancel"
    >
      <a-form :form="form">
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="状态"
        >
          <a-input
              v-decorator="[
          '状态',
          { rules: [{ required: checkNick, message: '请输入当前技能的状态' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="state"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="技能名称"
        >
          <a-input
              v-decorator="[
          '技能名称',
          { rules: [{ required: checkNick, message: '请输入技能名称' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="skillName"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="使用日期"
        >
          <a-input
              v-decorator="[
          '使用日期',
          { rules: [{ required: checkNick, message: '请输入技能的使用日期' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="useDate"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="使用日期(月):"
        >
          <a-input
              v-decorator="[
          '使用时间(月)',
          { rules: [{ required: checkNick, message: '请输入技能的使用时间(月)' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="useMouth"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="熟练程度"
        >
          <a-input
              v-decorator="[
          '熟练程度',
          { rules: [{ required: checkNick, message: '请输入技能的熟练程度' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="content"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="备注"
        >
          <a-input
              v-decorator="[
          '备注',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="remarked"
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
import bus from './eventBus.js'


const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8 },
};
const formTailLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8, offset: 4 },
};
export default {
  data() {
    return {
      state:'',
      skillName:'',
      useDate:'',
      useMouth:'',
      content:'',
      remarked:'',
      ModalText: 'Content of the modal',
      visible: false,
      confirmLoading: false,
      formItemLayout,
      formTailLayout,
      form: this.$form.createForm(this, { name: 'dynamic_rule' }),
    };
  },
  methods: {
    showModal() {
      this.visible = true;
    },
    handleOk() {
      this.ModalText = 'The modal will be closed after two seconds';
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
      bus.$emit("skillChange",this.state,this.skillName,this.useDate,this.useMouth,this.content,this.remarked)
    },
    handleCancel() {
      console.log('Clicked cancel button');
      this.visible = false;
    },
  },
  check() {
    this.form.validateFields(err => {
      if (!err) {
        console.info('success');
      }
    });
  },
  handleChange(e) {
    this.checkNick = e.target.checked;
    this.$nextTick(() => {
      this.form.validateFields(['nickname'], { force: true });
    });
  },
}
</script>
<style>
.button {
  position: relative;
  left: 700px;
  float: left;
}
</style>