<template>
  <div>
  <div style="width: 1300px; margin: 0 auto">
    <a-descriptions title="xxx简历" bordered>
      <a-descriptions-item label="姓名" v-model="name">{{name}}</a-descriptions-item>
      <a-descriptions-item label="身份证号码" :span="2" v-model="ID">
        {{ ID }}
      </a-descriptions-item>
      <a-descriptions-item label="标题" v-model="title"> {{title}} </a-descriptions-item>
      <a-descriptions-item label="出生日期" v-model="birthday"> {{ birthday }} </a-descriptions-item>
      <a-descriptions-item label="求职岗位" v-model="state"> {{ state }} </a-descriptions-item>
      <a-descriptions-item label="期望薪资" v-model="money"> {{money}} </a-descriptions-item>
      <a-descriptions-item label="邮箱" :span="2" v-model="email">
        {{ email }}
      </a-descriptions-item>
      <a-descriptions-item label="手机号码" v-model="tel"> {{ tel }}</a-descriptions-item>
      <a-descriptions-item label="想在哪里工作" v-model="place"> {{place}} </a-descriptions-item>
      <a-descriptions-item label="现居住地" v-model="nowplace"> {{nowplace}} </a-descriptions-item>

      <a-descriptions-item label="婚姻状况" v-model="marry"> {{marry}} </a-descriptions-item>
      <a-descriptions-item label="最高学历" v-model="highestEdu"> {{highestEdu}} </a-descriptions-item>
      <a-descriptions-item label="所学专业" v-model="major">
        {{major}}
      </a-descriptions-item>
      <a-descriptions-item label="身高" v-model="height"> {{height}} </a-descriptions-item>
      <a-descriptions-item label="健康情况" v-model="health"> {{health}} </a-descriptions-item>
      <a-descriptions-item label="民族" v-model="nation">{{nation}} </a-descriptions-item>
      <a-descriptions-item label="体重" v-model="weight"> {{weight}} </a-descriptions-item>
      <a-descriptions-item label="政治面貌" :span="1" v-model="political">
        {{political}}
      </a-descriptions-item>
      <a-descriptions-item label="简历树" :span="1">
        <template>
          <div>
            <a-button type="primary" @click="showModal">
              点击打开简历树
            </a-button>
            <a-modal
                title="Title"
                :visible="visible2"
                :confirm-loading="confirmLoading"
                @ok="handleOk"
                @cancel="handleCancel"
            >
              <a-card style="width: 300px">
                <p>Card content</p>
                <p>Card content</p>
                <p>Card content</p>
              </a-card>
              <template>
                <div class="tree">
                <a-timeline mode="alternate">
                  <a-timeline-item v-for="(tree) in trees" :key="tree.experience">{{tree.experience}}</a-timeline-item>
                  <a-timeline-item>
                    <a-icon slot="dot" type="clock-circle-o" style="font-size: 16px;" />
                    Technical testing 2015-09-01
                  </a-timeline-item>
                </a-timeline>
                </div>
              </template>
            </a-modal>
          </div>
        </template>
      </a-descriptions-item>
      <a-descriptions-item label="兴趣爱好" :span="3" v-model="hobby">
        {{hobby}}
      </a-descriptions-item>
    </a-descriptions>
    <template>
      <a-table :columns="columns" :data-source="dataSource" bordered>
        <template
            v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
            :slot="col"
            slot-scope="text, record"
        >
          <div :key="col">
            <a-input
                v-if="record.editable"
                style="margin: -5px 0"
                :value="text"
                @change="(e) => handleChange(e.target.value, record.key, col)"
            />
            <template v-else>
              {{ text }}
            </template>
          </div>
        </template>
      </a-table>
    </template>
    <template>
      <a-table :columns="columns2" :data-source="dataSource" bordered>
        <template
            v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
            :slot="col"
            slot-scope="text, record"
        >
          <div :key="col">
            <a-input
                v-if="record.editable"
                style="margin: -5px 0"
                :value="text"
                @change="(e) => handleChange(e.target.value, record.key, col)"
            />
            <template v-else>
              {{ text }}
            </template>
          </div>
        </template>
      </a-table>
    </template>
    <template>
      <a-table :columns="columns3" :data-source="dataSource" bordered>
        <template
            v-for="col in ['key','studyStyle', 'beginDate', 'endDate','schoolName','major','boss','praise','prove','tel','operation']"
            :slot="col"
            slot-scope="text, record"
        >
          <div :key="col">
            <a-input
                v-if="record.editable"
                style="margin: -5px 0"
                :value="text"
                @change="(e) => handleChange(e.target.value, record.key, col)"
            />
            <template v-else>
              {{ text }}
            </template>
          </div>
        </template>
      </a-table>
    </template>
  </div>
    <a-button type="primary" @click="showDrawer" style="margin-top: 10px"> <a-icon type="plus" /> 评价 </a-button>
    <a-drawer
        title="评价简历"
        :width="720"
        :visible="visible"
        :body-style="{ paddingBottom: '80px' }"
        @close="onClose"
    >
      <a-form :form="form" layout="vertical" hide-required-mark>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-item label="Name">
              <a-input
                  v-decorator="[
                  'name',
                  {
                    rules: [{ required: true, message: 'Please enter user name' }],
                  },
                ]"
                  placeholder="Please enter user name"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="24">
            <a-form-item label="个人素养评价">
              <a-textarea
                  v-decorator="[
                  'description',
                  {
                    rules: [{ required: true, message: '请输入个人素养评价' }],
                  },
                ]"
                  :rows="4"
                  placeholder="请输入个人素养评价"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="24">
            <a-form-item label="个人技术评价">
              <a-textarea
                  v-decorator="[
                  'description',
                  {
                    rules: [{ required: true, message: '请输入个人技术评价' }],
                  },
                ]"
                  :rows="4"
                  placeholder="请输入个人技术评价"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <span>
    <a-rate v-model="value" :tooltips="desc" />
    <span class="ant-rate-text">{{ desc[value - 1] }}</span>
  </span>
      </a-form>
      <div
          :style="{
          position: 'absolute',
          right: 0,
          bottom: 0,
          width: '100%',
          borderTop: '1px solid #e9e9e9',
          padding: '10px 16px',
          background: '#fff',
          textAlign: 'right',
          zIndex: 1,
        }"
      >
        <a-button :style="{ marginRight: '8px' }" @click="onClose">
          取消
        </a-button>
        <a-button type="primary" @click="onClose">
          提交
        </a-button>
      </div>
    </a-drawer>
  </div>
</template>

<script>

export default{
  components: {
  },
  data () {
    return {
      ModalText: 'Content of the modal',
      confirmLoading: false,
      form: this.$form.createForm(this),
      visible: false,
      visible2: false,
      value: 3,
      desc: ['terrible', 'bad', 'normal', 'good', 'wonderful'],
      name: 'XXX',
      ID:'411520200002111102',
      title: 'XXXXX',
      money:'12×30K',
      email:'111111111@qq.com',
      birthday:'2020-1-1',
      state:'技术岗位',
      tel:'10101010101',
      place:'上海',
      nowplace:'郑州',
      marry:'已婚',
      highestEdu:'硕士',
      major:'计算机科学与技术',
      height:'178',
      health:'优',
      nation:'汉',
      weight:'130',
      political:'共青团员',
      hobby:'运动',
      editingKey: '',
      count: 2,
      columns: [
        {
          title: '学历类型',
          dataIndex: 'studyStyle',
          scopedSlots: {customRender: 'studyStyle'},
        },
        {
          title: '开始日期',
          dataIndex: 'beginDate',
          scopedSlots: {customRender: 'beginDate'},
        },
        {
          title: '结束日期',
          dataIndex: 'endDate',
          scopedSlots: {customRender: 'endDate'},
        },
        {
          title: '学校名称',
          dataIndex: 'schoolName',
          scopedSlots: {customRender: 'schoolName'},
        },
        {
          title: '专业名称',
          dataIndex: 'major',
          scopedSlots: {customRender: 'major'},
        },
        {
          title: '干部名称',
          dataIndex: 'boss',
          scopedSlots: {customRender: 'boss'},
        },
        {
          title: '获奖证书',
          dataIndex: 'praise',
          scopedSlots: {customRender: 'praise'},
        },
        {
          title: '证明人',
          dataIndex: 'prove',
          scopedSlots: {customRender: 'prove'},
        },
        {
          title: '联系电话',
          dataIndex: 'tel',
          scopedSlots: {customRender: 'tel'},
        },
        {
          title: '备注',
          dataIndex: 'remarked',
          scopedSlots: {customRender: 'remarked'},
        },
      ],
      columns2: [
        {
          title: '工作类型',
          dataIndex: 'workStyle',
          scopedSlots: { customRender: 'workStyle' },
        },
        {
          title: '开始日期',
          dataIndex: 'beginDate',
          scopedSlots: { customRender: 'beginDate' },
        },
        {
          title: '结束日期',
          dataIndex: 'endDate',
          scopedSlots: { customRender: 'endDate' },
        },
        {
          title: '工作单位',
          dataIndex: 'workPlace',
          scopedSlots: { customRender: 'workPlace' },
        },
        {
          title: '工作岗位',
          dataIndex: 'major',
          scopedSlots: { customRender: 'major' },
        },
        {
          title: '职称',
          dataIndex: 'workName',
          scopedSlots: { customRender: 'workName' },
        },
        {
          title: '获奖证书',
          dataIndex: 'praise',
          scopedSlots: { customRender: 'praise' },
        },
        {
          title: '证明人',
          dataIndex: 'prove',
          scopedSlots: { customRender: 'prove' },
        },
        {
          title: '联系电话',
          dataIndex: 'tel',
          scopedSlots: { customRender: 'tel' },
        },
      ],
      columns3:[
        {
          title: '状态',
          dataIndex: 'state',
        },
        {
          title: '技能名称',
          dataIndex: 'skillName',
        },
        {
          title: '使用日期',
          dataIndex: 'useDate',
        },
        {
          title: '使用时间(月)',
          dataIndex: 'useMouth',
        },
        {
          title: '熟练程度',
          dataIndex: 'content',
        },
        {
          title: '备注',
          dataIndex: 'remarked',
        },
      ],
      trees:[
        {experience: '河南师范大学本科毕业 2022-06-30'},
        {experience: '清华大学研究生毕业 2025-06-30'},
        {experience: '中科院软件研究院2025-07-1'},
        {experience: '阿里达摩院 2030-07-01'},
      ],
    };
  },
  methods: {
    showDrawer() {
      this.visible = true;
    },
    onClose() {
      this.visible = false;
    },
    showModal() {
      this.visible2 = true;
    },
    handleOk() {
      this.ModalText = 'The modal will be closed after two seconds';
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible2 = false;
        this.confirmLoading = false;
      }, 2000);
    },
    handleCancel() {
      console.log('Clicked cancel button');
      this.visible2 = false;
    },
  },
}
</script>
<style>
.tree{
  margin-top:20px;
}
</style>