<template>
  <div>
    <div class="textBox">
      <ul>
        <li><a-icon type="fire" />通知：</li>
        <li>
          <transition name="slide">
            <p class="text" :key="text.id">{{text.val.title}}</p>
          </transition>
        </li>
      </ul>

    </div>
  </div>
</template>
<script>
export default {
  name: 'message',
  data () {
    return {
      message:'通知',
      textArr: [
        {title:'JAVA高级培训通知'},
        {title:'pthon高级培训通知'},
        {title:'js高级培训通知'},
        {title:'c高级培训通知'},
        {title:'php高级培训通知'},
      ],
      number: 0
    }
  },
  computed: {
    text () {
      return {
        id: this.number,
        val: this.textArr[this.number]
      }
    }
  },
  mounted () {
    this.startMove()
  },
  methods: {
    startMove () {
      // eslint-disable-next-line
      let timer = setTimeout(() => {
        if (this.number === this.textArr.length-1) {
          this.number = 0;
        } else {
          this.number += 1;
        }
        this.startMove();
      }, 2000);
    }
  }
}
</script>

<style scoped>
.textBox {
  width: 1200px;
  height: 40px;
  overflow: hidden;
  position: relative;
  text-align: left;
  margin: 20px auto;
  display:flex;
  align-items: center;
}
.text {
  height: 100%;
  position: absolute;
  bottom: 0;
  margin-bottom: 0em;
  display: flex;
  align-items: center;
}
ul{
  margin-bottom: 0em;

}
ul li:nth-of-type(1){
  color: red;
}
li{
  list-style: none;
  float: left;
  display: flex;
  align-items: center;
  font-size: 20px;
}
.slide-enter-active, .slide-leave-active {
  transition: all 0.4s linear;
}
.slide-enter{
  transform: translateY(20px);
  opacity: 0.5;
}
.slide-leave-to {
  transform: translateY(-20px);
  opacity: 0;
}
.anticon-fire{
  margin: 0px 10px;
}
</style>