<template>
  <div class="login">
  <div class="right">
    <img src="../../assets/newlogo.png" alt="">
    <table>
      <tr><td colspan="3" style="text-align: left;height: 50px;"><h3 style="font-size: 36px;margin-left: 60px">欢迎注册！</h3></td></tr>
      <tr><td colspan="3"  style="text-align: left;padding-left: 60px;font-size: 15px;">已有账号?<router-link to="/login"><span style="margin-left: 10px;font-size: 15px;color: #0ae754">登录</span></router-link></td></tr>
      <tr>
        <td>用户类型：</td>
        <td>
          <select style="padding-left: 5px;border: 1px solid black" id="group" value="1"  @change="changeProduct($event)">
            <option value="1" ref="person">个人</option>
            <option value="2" ref="company">企业</option>
          </select>
        </td>
        <td style="width: 130px"></td>
      </tr>
      <tr>
        <td>注册账号：</td>
        <td><input type="text" placeholder="注册账号" ref="account" @blur="blur" onkeyup="value=value.replace(/[^\d]/g,'')"></td>
        <td style="text-align: left;width: 100px; height: 50px;" v-show="var1"><a-icon style="color: #00BB29" type="check-circle" />检测通过</td>
        <td style="text-align: left;width: 130px; height: 50px;" v-show="var2"><a-icon style="color: red" type="close-circle" />请输入6位账号</td>
        <td style="text-align: left;width: 130px; height: 50px;" v-show="var9"><a-icon style="color: red" type="close-circle" />已注册</td>
      </tr>
      <tr>
        <td>密码：</td>
        <td><input type="password" placeholder="密码" ref="pad1" @blur="blur1"></td>
        <td style="text-align: left;width: 100px; height: 50px;" v-show="var3"><a-icon style="color: #00BB29" type="check-circle" />检测通过</td>
        <td style="text-align: left;width: 130px; height: 50px;" v-show="var4"><a-icon style="color: red" type="close-circle" />请输入6-8位密码</td>
      </tr>
      <tr>
        <td>确认密码：</td>
        <td><input type="password" placeholder="确认密码" ref="pad2" @blur="blur2"></td>
        <td style="text-align: left;width: 100px; height: 50px;" v-show="var5"><a-icon style="color: #00BB29" type="check-circle" />检测通过</td>
        <td style="text-align: left;width: 130px; height: 50px;" v-show="var6"><a-icon style="color: red" type="close-circle" />两次密码不一致</td>
        <td style="text-align: left;width: 130px; height: 50px;" v-show="var7"><a-icon style="color: red" type="close-circle" />请输入6-8位密码</td>
      </tr>
      <tr>
        <td></td>
        <td >
          <div class="btn">
            <router-link :to="address"><button @click="regis" style="color: white">注册</button></router-link>
          </div>
        </td>
      </tr>
    </table>
  </div>
    <login v-if="false"></login>
  </div>
</template>
<script>
import login from "@/views/index/login";
export default {
  name: "register",
  data(){
    return{
      img: require("@/assets/login.jpg"),
      ProductActive:"1",
      var1:false,
      var2:false,
      var3:false,
      var4:false,
      var5:false,
      var6:false,
      var7:false,
      var9:false,
      address:'',
      personalusername:[
        {
          name:"123456",
          password:123456
        }
      ],
      companyusername:[
        {
          name:"654321",
          password:654321
        }
      ],
    }
  },
  methods:{
    changeProduct(event) {
      this.ProductActive = event.target.value;
    },
    blur(){
     this.var1 = false;
     this.var2 = false;
     this.var9 = false;
      if (this.$refs.account.value.length == 6){
        if (this.ProductActive == 1){
          var var10 = false
          for (let i = 0; i < this.personalusername.length; i++) {
            if (this.personalusername[i].name.toString() == this.$refs.account.value.toString()){
              var10 = true
              break;
            }
            }
          if (var10){
            this.var9 = true
          }else{
            this.var1 = true
          }
          }
        else if (this.ProductActive == 2){
          var var11 = false
          for (let i = 0; i < this.companyusername.length; i++) {
            if (this.companyusername[i].name.toString() == this.$refs.account.value.toString()){
              var11 = true
              break;
            }
          }
          if (var11){
            this.var9 = true
          }else{
            this.var1 = true
          }
        }
        }
      else {
        this.var2 = true
      }
    },
    blur1(){
      this.var3 = false;
      this.var4 = false;
      if (this.$refs.pad1.value.length>=6 && this.$refs.pad1.value.length <= 8){
        this.var3 = true
      }
      else {
        this.var4 = true
      }
    },
    blur2(){
      this.var5 = false;
      this.var6 = false;
      this.var7 = false
      if (this.$refs.pad2.value.length>=6 && this.$refs.pad2.value.length <= 8){
        this.$refs.pad1.value.split("")
        this.$refs.pad2.value.split("")
        let var8 = false
        for (let i = 0; i < this.$refs.pad1.value.length; i++) {
          if (this.$refs.pad1.value.split("")[i]==this.$refs.pad2.value.split("")[i]){
            var8 = true
          }else {
            this.var6 = true
            return
          }
        }
        if (var8){
          this.var5 = true
        }
      }
      else {
        this.var7 = true
      }
    },
    regis(){
      if (this.var1&&this.var3&&this.var5){
        if (this.ProductActive == 1){
          this.personalusername.push({ name:this.$refs.account.value,password:this.$refs.pad2.value })
          this.$store.state.list = this.personalusername
          this.address = '/login'
        } else{
          this.companyusername.push({ name:this.$refs.account.value,password:this.$refs.pad2.value })
          this.$store.state.list1 = this.companyusername
          this.address = '/login'
        }
      }
      else {
        alert('注册失败，请重新注册！')
      }

    }
  },
  components:{
    login
  }
}
</script>

<style scoped>
td{
  width: 300px;
  height: 50px;
}
#group{
  border-radius: 3px;
  width: 270px;
  height: 40px;
  outline: none;
}
table tr td:nth-of-type(1){
  text-align: right;
  width: 130px;
}
.login{
  width: 100%;
  height:938px;
  display: flex!important;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1
}
.right{
  height: 500px;
  width:800px;
  background-color: white;
  position: absolute;
  opacity: 0.9;
  border-radius: 8px;
  box-shadow: 0 0 8px rgba(0,0,0,.1);
  display: flex;
  justify-content: center;
  align-items: center;
}
input{
  width: 270px;
  height: 40px;
  outline: none;
  border:1px solid black;
  border-radius: 3px;
  font-size: 15px;
  padding-left: 10px;
}
.yan input:nth-of-type(1){
  width: 150px;
}
.yan input:nth-of-type(2){
  width: 100px;
}
.btn button{
  width: 270px!important;
  background-color: deepskyblue;
  border: none;
  outline: none;
  height: 40px;
  border-radius: 5px;
}
.btn button:hover{
  cursor: pointer;
}
.btn{
  display: flex;
  justify-content: center;
  margin-top: 10px;
}
img{
  width: 80px;
  height: 80px;
  position: absolute;
  top: 20px;
  left: 50px;
}
table{
  margin-left: 50px;
}
</style>