<template>
  <div>
    <div class="edit">
      <el-row>
        <el-col :span="22">
          <h1>专业技能</h1>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" icon="el-icon-edit" @click="handleAddClick"
            >增加</el-button
          >
        </el-col>
      </el-row>
    </div>
    <div>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" :index="indexMethod" width="80">
        </el-table-column>
        <el-table-column prop="skillname" label="技能名称" width="180">
        </el-table-column>
        <el-table-column prop="usedate" label="使用日期"> </el-table-column>
        <el-table-column prop="usetime" label="使用时间(月)"> </el-table-column>
        <el-table-column prop="proficiency" label="熟练程度"> </el-table-column>
        <el-table-column prop="remark" label="备注"> </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click.native.prevent="deleteRow(scope.$index,tableData)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      :title="fromType == 'edit' ? '编辑' : '增加'"
      :visible.sync="addBox"
      width="50%"
      :before-close="handleClose"
    >
      <div>
        <form
        :model="fromData"
        >
          <div class="inputrestyle inputprepend">
            <input
              type="text"
              placeholder="技能名称"
              class="inputarea"
              v-model="fromData.skillname"
              style="border-bottom: none; border-radius: 4px 4px 0 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="使用日期"
              class="inputarea"
              v-model="fromData.usedate"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="使用时间（月）"
              class="inputarea"
              v-model="fromData.usetime"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="熟练程度"
              class="inputarea"
              v-model="fromData.proficiency"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input type="text" placeholder="备注" v-model="fromData.remark" class="inputarea" />
          </div>
          <div>
            <input
              class="saveSubmit"
              value="保存"
              @click="saveData"
            />
          </div>
        </form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          skillname: "Java",
          usedate: "2020-09",
          usetime: "12",
          proficiency: "掌握",
          remark: "",
        },
        {
          skillname: "Java",
          usedate: "2020-09",
          usetime: "12",
          proficiency: "掌握",
          remark: "",
        },
        {
          skillname: "Java",
          usedate: "2020-09",
          usetime: "12",
          proficiency: "掌握",
          remark: "",
        },
        {
          skillname: "Java",
          usedate: "2020-09",
          usetime: "12",
          proficiency: "掌握",
          remark: "",
        },
      ],
      addBox: false,
      fromType: 'new',
      arrIndex: 0,
      page:1,
      fromData: {
          skillname: "",
          usedate: "",
          usetime: "",
          proficiency: "",
          remark: "",
      },
      editData: {
          skillname: "",
          usedate: "",
          usetime: "",
          proficiency: "",
          remark: "",
        }
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    handleEdit(index, row) {
      this.fromType = 'edit'
      this.addBox = true
      this.arrIndex = index
      this.editData = Object.assign({}, row);
      this.fromData = this.editData
    },
    deleteRow(index,rows){
      rows.splice(index,1);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClose(done) {
      done();
    },
    handleAddClick() {
      this.fromType = 'new'
      this.addBox = true;
      this.clearFromData()
    },
    clearFromData () {
      this.fromData = {
          skillname: "",
          usedate: "",
          usetime: "",
          proficiency: "",
          remark: "",
      }
    },
    saveData() {
      if (this.fromType == 'new') {
        this.tableData.push(this.fromData)
      }
      if (this.fromType == 'edit') {
        this.tableData.splice(this.arrIndex, 1, this.fromData)
      }
      this.clearFromData()
      this.addBox = false
    }
  },
};
</script>

<style scoped>
::v-deep .el-table th,
::v-deep .el-table td {
  text-align: center !important;
}
.icon {
  margin: 10px 0px;
}
</style>

<style >
.block {
  text-align: center;
}
.edit {
  padding: 20px;
}

.inputprepend {
  position: relative;
  width: 100%;
}
.inputrestyle {
  margin-bottom: 0;
}
.inputarea {
  width: 92%;
  height: 50px;
  margin-bottom: 0;
  padding: 4px 12px 4px 35px;
  border: 1px solid #c8c8c8;
  border-radius: 0 0 4px 4px;
  background-color: hsla(0, 0%, 71%, 0.1);
  vertical-align: middle;
  color: inherit;
  font: inherit;
  margin: 0;
  outline: none;
}
.saveSubmit {
  margin-top: 20px;
  width: 150px;
  padding: 9px 18px;
  margin: 20px auto 0;
  font-size: 18px;
  border: none;
  border-radius: 25px;
  color: #fff;
  background: #42c02e;
  cursor: pointer;
  outline: none;
  display: block;
  clear: both;
}
.saveSubmit:hover {
  background: #3db922;
}
</style>
