<template>
  <div>
    <div class="top-message">全国（包含中央企业在内的）招聘信息公开</div>
    <div class="wrapper" id="d-container">
      <div class="inner clearfix">
        <div class="infobox">
          <div class="article" frag="窗口3" portletmode="simpleArticleAttri">

            <div class="arti_title">重庆市潼南区2020年公开招聘“大学生村官”为事业单位工作人员简章</div>
            <p class="arti_metas"><span class="arti_update">发布时间：2020-12-15</span></p>
            <div class="box01">
          <pre class="consent9">
    为加强人才队伍建设，规范进人行为，根据《事业单位人事管理条例》《重庆市事业单位公开招聘人员实施办法》（渝人发〔2006〕44号）和《关于进一步规范事业单位
  公开招聘工作的通知》（渝人社发〔2011〕326号）及市人力社保局《关于印发(重庆市事业单位考核招聘工作人员办法)等6个公开招聘配套文件的通知》（渝人社发〔201
  6〕281号）《关于从2017年选聘大学生村官中招考公务员和事业单位工作人员的通知》（渝委组〔2020〕16号）等有关规定，经市人力社保局备案，现面向潼南区2020年
  服务期满且考核合格的“大学生村官”公开招聘事业单位工作人员。

  一、招聘原则

    坚持公开、平等、竞争、择优方针，按照德才兼备的标准，采取考试的方式进行。

  二、招聘单位及人数

    本次招聘人数5名,具体招聘单位及岗位见附件1。

  三、招聘范围和对象

    （一）基本条件

    1.具有中华人民共和国国籍；

    2.遵守宪法和法律；

    3.具有良好的品行；

    4.适应岗位要求的身体条件；

    5.符合岗位所需的其他条件；

    6.具备国家规定的该岗位所需的必要条件；

    7.潼南区2020年服务期满且考核合格的大学生村官。

    （二）以下人员不纳入本次招聘范围

      曾因犯罪受过刑事处罚或曾被开除公职的人员；刑罚尚未执行完毕或属于刑事案件被告人、犯罪嫌疑人，司法机关尚未撤销案件、检察机关尚未作出不起诉决定或人民法
    院尚未宣告无罪的人员；尚未解除党纪、政纪处分或正在接受纪律审查的人员；因违反法律法规或单位人事纪律等被单位辞退或解聘未满5年的原机关事业单位工作人员；
    因违反机关事业单位工作人员招录（聘）纪律而处于禁考期的人员；最高人民法院公布的失信被执行人；国家有关部委联合签署备忘录明确的失信情形人员。

      现役军人；在读的非应届高校毕业生；试用期内的机关事业单位人员；市内未满机关事业单位招录（聘）公告中或双方签定的事业单位聘用合同中约定的最低服务期限的
    机关事业单位工作人员；尚未建立人事关系但参加我市机关事业单位招录（聘）已按程序完成体检或考察的拟聘用人员；我市公费培养、定向到乡镇教育（医疗卫生）机构
    就业，未满服务期限或本简章发布之日前未按规定程序解除培养及就业协议的师范（医学）生。

    法律法规规定不得聘用为事业单位工作人员的其他情形人员。

    （三）年龄计算

    本简章所要求的年龄计算截止时间为2020年12月24日，如“35周岁以下”，指未满36周岁，在1984年12月25日及以后出生，依次类推。

    （四）毕业（学位）证书及专业

    报考人员应凭已取得的毕业（学位）证书报考且须在报考资格审查时提交学历（学位）证书。

    （五）特别说明

    本简章所要求的条件中，所指“以上”“以下”“以前”“以后”均包含本级（数），如35周岁以下，均含35周岁；管理9级以上，均含管理9级，以此类推。

    本简章所涉及的时间节点，除明确约定外，均以本简章发布之日计算。

    考生在报名及考核时应按疫情防控要求，做好个人自身防护，若因健康风险和防疫要求等情况导致未能参加考试的后果由本人承担。

  四、报名及资格审核

    本次招聘采取现场报名方式进行，任何单位和个人不得以任何理由拒绝符合报考条件的人员报名应聘并参加考试考核。

    本次招聘所有岗位均不报考到具体单位，按最后总成绩由主管部门组织考生从高到低依次选岗，选岗时间另行通知。

    1.报名时间：2020年12月24日9：00-12：00。

    2.报名地点：潼南区人力资源和社会保障局522会议室。

    3.采取现场审核的方式进行资格审查,报名所需材料为毕业证、学位证、身份证。现场确认应聘人员提供的报名材料与报考资格的相符性。资格查审合格者，在报名地点当
    场领取《准考证》，按《准考证》上载明的时间、地点和其他相关要求，携带身份证和《准考证》前往参加考试考核。资格查审不合格的，向应聘人员说明原因。

  五、考试

    本次招聘考试包括笔试和面试。

    （一）笔试。

    1.时间初定于2020年12月26日（星期六），具体时间及考试地点详见《准考证》。

    2.笔试时间为90分钟，分值为100分。笔试内容为《综合知识测试》。笔试成绩须达到60分，方可进入本次招聘后续环节。

    （二）面试。

    面试中分值为100分,面试方式为结构化面试，时间为10分钟以上。具体时间及地点详见准考证。

    面试成绩未达到60分者，不合格，不得确定为体检人选。

    面试按照《重庆市事业单位公开招聘工作人员面试办法》（渝人社发〔2016〕281号）等文件规定执行。未按规定时间到指定地点参加面试的，取消本次面试资格。面试成

    （三）考试总成绩计算。

    考试总成绩＝笔试成绩×50%+面试成绩×50%。

    总成绩采用百分制计算，精确到小数点后两位数。

    考试总成绩及进入体检人员名单在重庆市潼南区人力资源和社会保障局网站上进行公布。

  六、体检和考察

      体检人选按拟招聘名额等额确定。体检标准参照《关于修订〈公务员录用体检通用标准（试行）及〈公务员录用体检操作手册（试行）〉有关内容的通知》（人社部发
   〔2016〕140号）等规定，结合本行业或岗位实际要求执行，并按规定填写《事业单位工作人员聘用体检表》。

      体检在指定的具有资质的县级以上医疗卫生机构（一般二级甲等以上）进行。除按相关规定应在当场或当天复检并确认体检结果的项目外，招聘单位或受检人对体检结论
    有异议的，可在接到体检结论通知之日起7日内书面提出复检申请，经事业单位人事综合管理部门同意后到指定医院进行一次性复检，体检结论以复检结论为准。

      体检合格者，由主管部门会同招聘单位按照《重庆市事业单位公开招聘工作人员考察办法》，对拟聘人员进行全面考察，包括对其政治思想素质、道德品质修养、能力素
    质、遵纪守法情况、日常学习工作情况以及是否需要回避等统一组织考察。考察人员名单确定后，应当及时通知考生本人。

      考察应坚持“凡进必审”，考察小组要按照“谁审查、谁负责”、考察留痕的原则，严格审查拟聘人员的人事档案、诚信记录、违法犯罪记录，确认拟聘人员提供的报名材料
    及有关报考资格的各类信息材料是否属实。注重采取实地考察、延伸考察、官方网站查询【中国高等教育学生信息网（http://chsi.com.cn）查询学历、中国学位与研究
    生教育信息网（http://www.cdgdc.edu.cn/）查询学位、中国执行信息公开网（http://zxgk.court.gov.cn）查询最高人民法院公布的失信被执行人、“信用中国”网站
    （http://www.creditchina.gov.cn）查询国家有关部委联合签署备忘录明确的失信情形人员】等方式进行查证。考察结论为不合格的，招聘方在通知结论的同时向拟聘
    人员说明原因。

      凡发现档案材料或者信息涉嫌造假的，应当立即查核，未核实前，一律暂停聘用；发现应聘人员提供虚假材料、隐瞒事实真相，或提供的材料或信息不实影响审核结果的
    或干扰、影响考察单位客观公正作出考察结论的，一律不予聘用，人员信息由招聘单位报事业单位人事综合管理部门备案。

  七、公示

    考试、体检、考察等有关事宜，在重庆市潼南区人力资源和社会保障局网站（网址：http://old.cqtn.gov.cn/www/tnrsj/）上发布，请考生及时关注。

    拟聘人员名单在重庆市人力资源和社会保障局（http://rlsbj.cq.gov.cn/）上进行公示，公示时间为7个工作日。公示内容包括姓名、性别、出生年月、毕业院校及毕业
   时间、所学专业及学历（学位）、考试考核总成绩以及岗位招聘条件所要求的工作经历、职称、职业（执业）资格等其他应公示事项。

  八、聘用及待遇

    经公示无异议或异议信息经核实不影响聘用的拟聘用人员，由用人单位及主管部门将相关资料报送同级事业单位人事综合管理部门备案，对符合招聘条件的人员应及时办理
   聘用手续。

    经备案同意聘用的人员，由招聘单位法定代表人或者其委托人与受聘人员按照《重庆市事业单位试行人员聘用制度实施办法》（渝府发〔2003〕37号）和《重庆市人事局关
   于转发(事业单位聘用合同（范本）)的通知》（渝人发〔2006〕68号）等规定，签订《事业单位聘用合同》，确立人事关系，相关待遇按重庆市有关规定执行。

    本次考核招聘全部人员一经聘用，须在潼南区工作满5年（其中在招聘单位工作满3年）方能交流、调动；未满规定服务期者，解除人事关系，承担相应违约责任。

  九、纪律与监督

    公开招聘事业单位工作人员，是公开选拔优秀人才的重要渠道，必须严肃人事工作纪律，确保招聘工作顺利进行。各有关单位要严格执行《事业单位公开招聘违纪违规行为
   处理规定》（人力资源和社会保障部令第35号）、《事业单位人事管理回避规定》（人社部规〔2019〕1号），认真贯彻《关于印发〈重庆市事业单位考核招聘工作人员办法
   〉等6个公开招聘配套文件的通知》（渝人社发〔2016〕281号）等有关政策规定，自觉接受纪检监察机关和社会各界的监督，若有违纪违规行为，按规定追究有关责任。

    公开招聘过程中凡发现报考人员档案材料或者信息涉嫌造假的，应当立即查核，未核实前，暂停聘用；发现报考人员提供虚假材料、隐瞒事实真相，或提供的材料、信息
   不实影响审核结果的，或干扰、影响考察单位客观公正进行考察的，给予考察不合格结论，一律不予聘用。报考人员提供伪造的身份证件和招聘公告要求的学历（学位）、
   职（执）业资格等材料的，一经查实，视为品行不端及不诚信行为，由区县事业单位人事综合管理部门报市级事业单位人事综合管理部门备案并计入个人诚信档案，从本公
   告发布之日起5年内限制招聘为我市事业单位工作人员。

  十、其他事宜

    招聘过程中涉及的有关问题，请按以下方式咨询：

    （一）本简章由潼南区人力资源和社会保障局负责解释；

    （二）公开招聘政策咨询电话023-44558307；

    （三）重庆市事业单位公开招聘问题和情况反映信箱：2522912065@qq.com（该邮箱不接受简历）。

