<template>
  <div id="warp">
    <div class="sel">
      <span class="top">你选择的是：</span>
      <span v-for="(item,index) in arr" :key="index" >
      <span>{{item.name}}</span>
      <a style="color: red;" href="javascript:;" @click="remove(index,item.id)">X</a>
    </span>
      <div >
        <ul>
          <li v-for="(item,index) in phoneData" :key="index">
            <span>{{item.title}}:</span>
            <a href="javascript:;" v-for="(option,i) in item.list" :class="{active:item.index===i}" @click="listBundle(option,index,i)" :key="i" >{{option}}
            </a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name:'sel2',
  data() {
    return {
      phoneData: [{
        title: '工作地点',
        list: ['全国', '北京', '上海', '广州', '深圳', '杭州', '成都', '武汉', '苏州']
      },
        {
          title: '工作经验',
          list: ['不限', '应届毕业生', '1年以下', '1-3年', '3-5年','5-10年','10年以上']

        },
        {
          title: '学历要求',
          list: ['不限', '大专', '本科', '硕士', '博士']
        },
        {
          title: '行业领域',
          list: ['不限', '移动互联网', '电子商务', '金融', '企业服务', '游戏', '硬件']
        }
      ],
      arr: []
    }
  },
  methods: {
    listBundle(option, index, i) {
      this.phoneData[index].index = i;
      let param = {
        id: index,
        name: option
      }

      this.arr.push(param)
      for (let i = 0; i < this.arr.length; i++) {
        for (let j = i + 1; j < this.arr.length; j++) {
          if (this.arr[i]['id'] == this.arr[j]['id']) {
            this.arr[i]["name"] = this.arr[j]['name']
            this.arr.splice(j, 1)
          }
        }
      }
    },
    remove(index, id) {
      this.$delete(this.arr, index, 1);
      this.phoneData[id].index = -1;
    }
  }
}
</script>
<style scoped>
#warp {
  width: 1200px;
  height: 260px;
  margin: 0 auto;
  background-color: #fafafa;
  border: 1px solid #ededed;
  margin-top: 50px;
}

li {
  list-style: none;
  float: left;
  width: 100%;
  text-align: left;
  height: 45px;
}
li span{
  color: black;
  font-weight: 700;
  margin-right: 20px;
}
#warp div {
  width: 100%;
  height: 100%;
}
.top{
  display: inline-block;
  height: 50px;
}
a {
  display: inline-block;
  margin: 0px 10px;
  text-decoration: none;
}
.sel{
  padding-top: 20px;
  margin-left: 20px;
  text-align: left;
}
.active {
  background-color: #00b38a;
  color: #fff;
}
</style>