<template>
  <div class="button">
    <a-button type="primary" @click="showModal">
      增加工作经历
    </a-button>
    <a-modal
        title="增加工作经历"
        :visible="visible"
        :confirm-loading="confirmLoading"
        @ok="handleOk"
        @cancel="handleCancel"
    >
      <a-form :form="form">
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="序号"
        >
          <a-input
              v-decorator="[
          '序号',
          { rules: [{ required: checkNick, message: '请输入当前技能的状态' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="key"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="工作类型"
        >
          <a-input
              v-decorator="[
          '工作类型',
          { rules: [{ required: checkNick, message: '请输入当前技能的状态' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workStyle"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="开始日期"
        >
          <a-date-picker style="width: 200px" v-model="beginDate"/>
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="结束日期"
        >
          <a-date-picker style="width: 200px" v-model="endDate" />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="工作单位"
        >
          <a-input
              v-decorator="[
          '使用时间(月)',
          { rules: [{ required: checkNick, message: '请输入技能的使用时间(月)' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workPlace"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="工作岗位"
        >
          <a-input
              v-decorator="[
          '熟练程度',
          { rules: [{ required: checkNick, message: '请输入技能的熟练程度' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="major"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="职称"
        >
          <a-input
              v-decorator="[
          '职称',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="workName"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="获奖证书"
        >
          <a-input
              v-decorator="[
          '获奖证书',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="praise"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="证明人"
        >
          <a-input
              v-decorator="[
          '证明人',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="prove"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="联系电话"
        >
          <a-input
              v-decorator="[
          '联系电话',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
              v-model="tel"
          />
        </a-form-item>
        <a-form-item
            :label-col="formItemLayout.labelCol"
            :wrapper-col="formItemLayout.wrapperCol"
            label="备注"
        >
          <a-input
              v-decorator="[
          '备注',
          { rules: [{ required: checkNick, message: '请输入备注' }] },
        ]"
              placeholder="Please input your nickname"
          />
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
import bus from "./eventBus.js"

const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8 },
};
const formTailLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8, offset: 4 },
};
export default {
  data() {
    return {
      workStyle:'',
      beginDate: '',
      endDate: '',
      workPlace:'',
      major:'',
      workName:'',
      praise:'',
      prove:'',
      tel:'',
      ModalText: 'Content of the modal',
      visible: false,
      confirmLoading: false,
      formItemLayout,
      formTailLayout,
      form: this.$form.createForm(this, { name: 'dynamic_rule' }),
    };
  },

  methods: {
    showModal() {
      this.visible = true;
    },
    handleOk() {
      this.ModalText = 'The modal will be closed after two seconds';
      this.confirmLoading = true;
      setTimeout(() => {
        this.visible = false;
        this.confirmLoading = false;
      }, 2000);
      bus.$emit('workChange',this.key,this.workStyle,this.beginDate,this.endDate,this.workPlace,this.major,this.workName,this.praise,this.prove,this.tel,);
    },
    handleCancel() {
      console.log('Clicked cancel button');
      this.visible = false;
    },
  },
  check() {
    this.form.validateFields(err => {
      if (!err) {
        console.info('success');
      }
    });
  },
  handleChange(e) {
    this.checkNick = e.target.checked;
    this.$nextTick(() => {
      this.form.validateFields(['nickname'], { force: true });
    });
  },
}
</script>
<style>
.button {
  position: relative;
  left: 50px;
  float: left;
}
</style>