<template>
  <div>
    <h2>公司实名认证</h2>

    <el-row class="el-row-button">
      <el-button type="warning" @click="dialogVisible = true">上传</el-button>
      <el-dialog title="上传证件" :visible.sync="dialogVisible" width="25%">
        <el-form ref="form" :model="form" label-width="120px">
          <el-form-item label="公司证件名称：">
            <el-input v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="证件描述：">
            <el-input v-model="form.describe"></el-input>
          </el-form-item>
          <el-form-item label="证件图片：">
           <el-upload
  class="upload-demo"
  action="https://jsonplaceholder.typicode.com/posts/"
  :on-preview="handlePreview"
  :on-remove="handleRemove"
  :before-remove="beforeRemove"
  multiple
  :limit="3"
  :on-exceed="handleExceed"
  :file-list="fileList">
  <el-button size="small" type="primary">点击上传</el-button>
  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
</el-upload>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
      </el-dialog>

    </el-row>

    <el-row class="el-row-table">
      <el-table
        :data="tableData"
        style="width: 100%"
        border
        stripe>
        <el-table-column
            type="index"
            label="序号 "
            width="50">
        </el-table-column>

        <el-table-column
            prop="name"
            label="公司证件名称"
            width="200">
        </el-table-column>

        <el-table-column
            prop="describe"
            label="证件描述"
            width="250">
        </el-table-column>
        <el-table-column
            label="证件图片">
          <el-upload
              class="upload-demo"
              action="https://jsonplaceholder.typicode.com/posts/"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :before-remove="beforeRemove"
              multiple
              :limit="3"
              :on-exceed="handleExceed"
              :file-list="fileList">
           
          </el-upload>
        </el-table-column>
      </el-table>
    </el-row>
    
  </div>
</template>

<script>
export default {
  
  name: "companycertification",
  data() {
    
    return {
       dialogVisible:false,

       form: {
        name: '',
        describe: '',
        picture: '',
      },
      tableData: [{
        name: '赵晓音',
        describe: '营业证',
        picture: ''
      }]
    }
    
  }
}
</script>

<style scoped>
.el-row-button{
  margin-top: 15px;
}
.el-row-table{
  margin-top: 15px;
}
</style>