<template>
  <div>
    <h2>公司缴费台账</h2>
    <el-row :gutter="30">
      <el-col :span="8" >
        <div>
        <el-input placeholder="请输入名字" clearable v-model="search" >
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      </el-col>


      <el-col :span="16"  >
        <div>
    <el-button type="primary" @click="dialogVisible = true" >充值</el-button>

    <el-dialog title="充值" :visible.sync="dialogVisible" width="30%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="账户余额">
          <el-input disabled="disabled"  v-model="form.money"  placeholder="5500¥"></el-input>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input v-model="form.dingdan"></el-input>
        </el-form-item>
        <el-form-item label="充值金额">
          <el-input v-model="form.money2"></el-input>
        </el-form-item>

<el-form-item label="支付方式">  
         <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          
    <el-tab-pane label="支付宝" name="first">
       <el-checkbox label="支付宝" name="type1"></el-checkbox>
    </el-tab-pane>

    <el-tab-pane label="网上银行" name="second">
      <el-checkbox-group v-model="form.type"  label-width="80px" >
        <el-radio v-model="radio" label="1">中国银行</el-radio>
        <el-radio v-model="radio" label="2">中国银行</el-radio>
        <el-radio v-model="radio" label="3">中国银行</el-radio>
        <el-radio v-model="radio" label="4">中国银行</el-radio>
        </el-checkbox-group>
    </el-tab-pane>

  </el-tabs>
</el-form-item>
       
     


      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
          </div>
      </el-col>
    </el-row>

    <el-table
        :data="tables"
        border
        style="width: 100%">
      <el-table-column
          type="index"
          label="序号 "
          width="50">
      </el-table-column>
      <el-table-column
          label="状态 "
          width="120">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.state">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
          prop="order"
          label="订单号"
          width="140">
      </el-table-column>
      <el-table-column
          prop="payment"
          label="缴费日期"
          width="140">
      </el-table-column>
      <el-table-column
          prop="amount"
          label="缴费金额"
          width="140">
      </el-table-column>
      <el-table-column
          prop="service"
          label="服务期数（月）"
          width="140">
      </el-table-column>
      <el-table-column
          prop="start"
          label="开始日期"
          width="140">
      </el-table-column>
      <el-table-column
          prop="end"
          label="结束日期"
          width="140">
      </el-table-column>
      <el-table-column
          prop="name"
          label="销售经办人"
          width="140">
      </el-table-column>
      <el-table-column
          prop="tel"
          label="联系电话"
          width="140">
      </el-table-column>
      <el-table-column
          prop="remarks"
          label="备注">
      </el-table-column>
    </el-table>


    <el-button-group>
      <el-button type="primary" icon="el-icon-arrow-left"  size="mini">上一页</el-button>
      <el-button type="primary"  size="mini">下一页<i class="el-icon-arrow-right el-icon--right" ></i></el-button>
    </el-button-group>

  </div>
</template>

<script>
export default {
name: "account",
  data() {
    return {
       radio: '1',
      search: '',
      dialogVisible:false,
      form: {
        type: [],
        name: '',
        number:'',
        department:'',
        role:'',
        post:'',
        area:'',
      },
      tableData: [{
        state:true,
        order:'1601150930',
        payment:'2020-12-30',
        amount:'200000',
        service:'12',
        start:'2020-12-30',
        end:'2021-12-30',
        name: '王小二',
        tel:'13833725790',
        remarks: '无'
      }, {
        state:true,
        order:'1601150231',
        payment:'2020-12-30',
        amount:'200000',
        service:'12',
        start:'2020-12-30',
        end:'2021-12-30',
        name: '王小二',
        tel:'13856893790',
        remarks: '无'
      }, {
        state:false,
        order:'1423670930',
        payment:'2020-12-30',
        amount:'200000',
        service:'12',
        start:'2020-12-30',
        end:'2021-12-30',
        name: '王小二',
        tel:'13894232406',
        remarks: '无'
      }, {
        state:true,
        order:'1467932430',
        payment:'2020-12-30',
        amount:'200000',
        service:'12',
        start:'2020-12-30',
        end:'2021-12-30',
        name: '王小二',
        tel:'15139546672',
        remarks: '无'
      }]
    }
  },
   methods:{
        isChoose(index){
          if(index==1){
              if(this.isChecked==1){
                  //改变原来的状态，就是为选中的状态，当点击的时候就取消掉选中的状态
                  this.isChecked=0;
              }else {
                  this.isChecked=1;
              }
          }else {
              if(this.isChecked==-1){
                  this.isChecked=0;
              }else {
                  this.isChecked=-1;
              }
          }
        }
    },

  computed: {
// 模糊搜索
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(data => {
          return Object.keys(data).some(key => {
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  }
}
</script>

<style scoped>
el-table{
  margin-top: 15px;
}
el-button-group{
  margin-top: 15px;
}
el-form-item2{
  width: 20px;
}

</style>