<template>
  <div>
    <div class="edit">
      <el-row>
        <el-col :span="22">
          <h1>个人缴费账目管理</h1>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" icon="el-icon-bank-card" @click="handleAddClick()">充值</el-button>
        </el-col>
      </el-row>
    </div>

    <div class="tab">
      <el-table :data="tableData" style="width: 120%">
        <el-table-column type="index" label="序号" :index="indexMethod" width="80">
        </el-table-column>
        <el-table-column prop="status" label="状态" width="100">
        </el-table-column>
        <el-table-column prop="ordernumber" label="订单号" width="100">
        </el-table-column>
        <el-table-column prop="paymentdate" label="缴费日期" width="120">
        </el-table-column>
        <el-table-column prop="payment" label="缴费金额" width="100">
        </el-table-column>
        <el-table-column
          prop="serviceperiod"
          label="服务期数（月）"
          width="120"
        >
        </el-table-column>
        <el-table-column prop="sdate" label="开始日期" width="120">
        </el-table-column>
        <el-table-column prop="endate" label="结束日期" width="120">
        </el-table-column>
        <el-table-column prop="rechargemethod" label="充值方式" width="120">
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="150">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="deleteRow(scope.$index, tableData)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5,10,15,20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="充值"
      :visible.sync="addBox"
      width="25%"
      :before-close="handleClose"
    >
      <div>
        <div class="inputprepend">
          <b>账户余额：</b>
        </div>
        <div class="inputprepend">
          <b>订单号：</b><input type="text" class="inputStyle" />
        </div>
        <div class="inputprepend">
          <b>充值金额：</b><input type="text" class="inputStyle" />
        </div>
        <p class="pStyle"><b>选择充值方式：</b></p>
        <div class="inputprepend paySpace">
          <input type="radio" name="payWay" value="alipay" />支付宝
          <input type="radio" name="payWay" value="ebank" />网上银行
        </div>
        <div>
          <input type="botton" class="saveSubmit" name="" id="" value="提交" />
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          status: "已缴纳",
          ordernumber: "1234-011",
          paymentdate: "2020-06",
          payment: "100",
          serviceperiod: "3",
          sdate: "2020-06",
          endate: "2020-09",
          rechargemethod: "支付宝",
          remark: "",
        },
        {
          status: "已缴纳",
          ordernumber: "1234-044",
          paymentdate: "2020-09",
          payment: "100",
          serviceperiod: "3",
          sdate: "2020-09",
          endate: "2020-12",
          rechargemethod: "网上银行",
          remark: "",
        },
        {
          status: "已缴纳",
          ordernumber: "1234-088",
          paymentdate: "2020-12",
          payment: "100",
          serviceperiod: "3",
          sdate: "2020-12",
          endate: "2021-3",
          rechargemethod: "支付宝",
          remark: "",
        },
      ],
      addBox: false,
      page:1,
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClose(done) {
      done();
    },
    handleAddClick() {
      this.addBox = true;
    },
  },
};
</script>

<style scoped>
::v-deep .el-table th,
::v-deep .el-table td {
  text-align: center !important;
}
.icon {
  margin: 10px 0px;
}

</style>

<style >
.edit {
  padding: 20px;
}
.inputprepend {
  position: relative;
  width: 100%;
  text-align: left;
  line-height: 35px;
}
.saveSubmit {
  margin-top: 20px;
  width: 150px;
  padding: 9px 18px;
  margin: 20px auto 0;
  font-size: 18px;
  border: none;
  border-radius: 25px;
  color: #fff;
  background: #42c02e;
  cursor: pointer;
  outline: none;
  display: block;
  clear: both;
  text-align: center;
}
.saveSubmit:hover {
  background: #3db922;
}
.paySpace{
  text-align: center;
}
.inputStyle{
    height: 25px;
    width: 200px;
    float: right;
}
.pStyle{
    text-align: left;
}
</style>
