<template>
  <div>
    <h2>公司职位管理</h2>

    <el-row :gutter="30">
      <el-col :span="8" ><div>
        <el-input placeholder="请输入名字" clearable v-model="search" >
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      </el-col>
      <el-col :span="16"  >
        
        <div>

            <el-button type="primary" @click="dialogVisible = true">添加</el-button>

            <el-dialog title="新增员工账号" :visible.sync="dialogVisible" width="50%">
              <el-form ref="form" :model="form" label-width="100px" :label-position="right">
                
                <el-form-item label="发布在:">
            <el-select type="text" v-model="aaa" placeholder="区域" >
            <el-option label="北京" value="beijing">北京</el-option>
            <el-option label="上海" value="shanghai">上海</el-option>
            </el-select>

             <el-select type="text" v-model="bbb" placeholder="全职招聘">
            <el-option label="全职招聘" value="quanzhi">全职招聘</el-option>
            <el-option label="兼职招聘" value="jianzhi">兼职招聘</el-option>
            </el-select>
        
                </el-form-item>
                
                <el-form-item label="职位标题：">
                  <el-input v-model="form.number"></el-input>
                </el-form-item>
                <el-form-item label="职位类别：">
                  <el-input v-model="form.department"></el-input>
                </el-form-item>
                <el-form-item label="招聘人数：">
                  <el-input v-model="form.role"></el-input>
                </el-form-item>
                <el-form-item label="学历：">
                  <el-input v-model="form.post"></el-input>
                </el-form-item>
                <el-form-item  label="工作年限：">
                   <el-col :span="15">
                    <el-input type="" placeholder="" v-model="form.age1" style="width: 100%;"></el-input>
                  </el-col>
                  <el-radio v-model="radio" label="1">应届生</el-radio>
                </el-form-item>
                 <el-form-item label="年龄：">
                   <el-col :span="8">
                    <el-input  v-model="form.age1" style="width: 80%;"></el-input>
                  </el-col>
                  <el-col class="line" :span="1">—</el-col>
                  <el-col :span="8">
                    <el-input   v-model="form.age2" style="width: 80%;"></el-input>岁
                  </el-col>
                </el-form-item>
                 <el-form-item  label="月薪：">
                   <el-col :span="13">
                    <el-input type="" placeholder="" v-model="form.age1" style="width: 100%;"></el-input>
                  </el-col>
                  <el-radio v-model="radio" label="1">有试用期</el-radio>
                </el-form-item>
                 <el-form-item label="工作时间：">
                  <el-input v-model="form.time"></el-input>
                </el-form-item>
                 <el-form-item label="职位职责：">
                  <el-input v-model="form.post2"></el-input>
                </el-form-item>
                <el-form-item label="任职要求：">
                  <el-input v-model="form.post2"></el-input>
                </el-form-item>

                 <el-form-item label="技能标签" >
                <el-checkbox-group v-model="form.type">
                  <el-checkbox label="ios" name="type"></el-checkbox>
                  <el-checkbox label="Java" name="type"></el-checkbox>
                  <el-checkbox label="ios" name="type"></el-checkbox>
                  <el-checkbox label="Java" name="type"></el-checkbox>
                </el-checkbox-group>
                </el-form-item>

                 <el-form-item label="福利" >
                <el-checkbox-group v-model="form.type">
                  <el-checkbox label="五险一金" name="type"></el-checkbox>
                  <el-checkbox label="交通补助" name="type"></el-checkbox>
                  <el-checkbox label="住房补助" name="type"></el-checkbox>
                  <el-checkbox label="年底双薪" name="type"></el-checkbox>
                  <el-checkbox label="话费补助" name="type"></el-checkbox>
                  
                  <el-button class="el-button-zdy" type="primary" @click="dialogVisible = true">自定义</el-button>

                </el-checkbox-group>
                </el-form-item>

                  <el-form-item>
                    
                  <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
                  <el-button type="primary" @click="subimtForm('ruleForm')">发布</el-button>
                  <el-checkbox label="把应聘简历发到我的邮箱" name="type"></el-checkbox>
           
                  </el-form-item>





                
              </el-form>
       
            </el-dialog>

          <el-button type="success" @click="open" class="el-button-fabu">发布</el-button>
      </div>
      </el-col>
    </el-row>

    <el-row class="el-row-table">
    <el-table
        :data="tables"
        style="width: 100%"
        border
        stripe>
      <el-table-column
          type="index"
          label="序号 "
          width="50">
      </el-table-column>
      <el-table-column
          prop="name"
          label="姓名 "
          width="120">
      </el-table-column>
      <el-table-column
          label="状态 "
          width="100">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.state">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
          prop="post"
          label="职位名称"
          width="180">
      </el-table-column>
      <el-table-column
          prop="department"
          label="公司部门"
          width="170">
      </el-table-column>
      <el-table-column
          prop="workaddress"
          label="工作地点">
      </el-table-column>
      <el-table-column
          prop="fabuaddress"
          label="发布城市"
          width="160">
      </el-table-column>
      <el-table-column
          prop="start"
          label="发布日期"
          width="160">
      </el-table-column>
      <el-table-column
          prop="refresh"
          label="刷新日期"
          width="160">
      </el-table-column>
      <el-table-column
          prop="end"
          label="结束日期"
          width="160">
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
              type="danger"
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-row>

    <!--  分页区  -->
    <el-row class="el-row-up-down">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="tableData.length">
      </el-pagination>
<!--      <el-button-group>-->
<!--        <el-button type="primary" icon="el-icon-arrow-left"  size="mini">上一页</el-button>-->
<!--        <el-button type="primary"  size="mini">下一页<i class="el-icon-arrow-right el-icon&#45;&#45;right" ></i></el-button>-->
<!--      </el-button-group>-->
    </el-row>
  </div>
</template>

<script>
export default {
name: "companypositionmanagement",
  data() {
    return {
      aaa:'',
      bbb:'',
      search: '',
      dialogVisible:false,
      form: {
        type: [],
        name: '',
        number:'',
        department:'',
        role:'',
        post:'',
        area:'',
      },
      queryInfo:{
        query:'',
        // 当前页数
        pagenum: 1,
        //当前每页显示多少数据
        pagesize: 2,
      },

      tableData: [{
        name:'李晓晓',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'王丙稀',
        state:false,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },{
        name:'张娜',
        state:true,
        post: '前端开发工程师',
        department:'工程部',
        workaddress: '北京',
        fabuaddress:'上海',
        start:'2020-12-30',
        refresh:'2020-12-30',
        end:'2020-12-30',
      },
      ]
    }
  },
  computed: {
// 模糊搜索
    tables () {
      const search = this.search
      if (search) {
        return this.tableData.filter(data => {
          return Object.keys(data).some(key => {
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.tableData
    }
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },

    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.queryInfo.pagesize = val;
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    open() {
      this.$confirm('', '是否进行发布操作', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        this.$message({
          type: 'success',
          message: '发布成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消发布'
        });
      });
    }
  }
}
</script>

<style scoped>
.el-row-table{
  margin-top: 15px;
}
.el-row-up-down{
  margin-top: 30px;
}
.el-button-fabu{
  margin-left: 15px;
}
el-form-item select{
  margin-left:10px;
  size: 10px;
}
.el-button-zdy{
  margin-left: 15px;
}

</style>