<template>
  <div>
    <div class="edit">
      <el-row>
        <el-col :span="22">
          <h1>培训经历管理</h1>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" icon="el-icon-edit" @click="handleAddClick"
            >增加</el-button
          >
        </el-col>
      </el-row>
    </div>
    <div class="tab">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" :index="indexMethod" width="80">
        </el-table-column>
        <el-table-column prop="traintype" label="培训类型" width="100">
        </el-table-column>
        <el-table-column prop="starttime" label="开始日期" width="100">
        </el-table-column>
        <el-table-column prop="endtime" label="结束日期" width="100">
        </el-table-column>
        <el-table-column prop="tschool" label="培训机构" width="100">
        </el-table-column>
        <el-table-column prop="tmessage" label="培训内容" width="100">
        </el-table-column>
        <el-table-column prop="job" label="干部名称" width="100">
        </el-table-column>
        <el-table-column prop="award" label="证书" width="100">
        </el-table-column>
        <el-table-column prop="witness" label="证明人" width="100">
        </el-table-column>
        <el-table-column prop="tel" label="联系方式" width="150">
        </el-table-column>
        <el-table-column prop="remark" label="备注" width="100">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="deleteRow(scope.$index, tableData)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="block">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="新增培训经历"
      :visible.sync="addBox"
      width="50%"
      :before-close="handleClose"
    >
      <div>
        <form action="sessions" accept-charset="UTF-8" method="POST">
          <div class="inputrestyle inputprepend">
            <input
              type="text"
              placeholder="机构名称"
              class="inputarea"
              style="border-bottom: none; border-radius: 4px 4px 0 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="开始日期"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="结束日期"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="培训内容"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="业余/全职"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="证明人"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <input
              type="text"
              placeholder="联系电话"
              class="inputarea"
              style="border-bottom: none; border-radius: 0"
            />
          </div>
          <div class="inputprepend">
            <textarea
              type="text"
              placeholder="备注"
              class="inputarea"
              style="padding: 15px 34px; height: 60px"
            ></textarea>
          </div>
          <div>
            <input
              type="submit"
              class="saveSubmit"
              name=""
              id=""
              value="保存"
            />
          </div>
        </form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          traintype: "公司培训",
          starttime: "2020-9",
          endtime: "2020-12",
          ttschool: "河南师范大学",
          tmessage: "html",
          job: "学生",
          award: "",
          witness: "操老师",
          tel: "12345678998",
          remark: "",
        },
        {
          traintype: "公司培训",
          starttime: "2020-9",
          endtime: "2020-12",
          ttschool: "河南师范大学",
          tmessage: "html",
          job: "学生",
          award: "",
          witness: "操老师",
          tel: "12345678998",
          remark: "",
        },
        {
          traintype: "公司培训",
          starttime: "2020-9",
          endtime: "2020-12",
          ttschool: "河南师范大学",
          tmessage: "html",
          job: "学生",
          award: "",
          witness: "操老师",
          tel: "12345678998",
          remark: "",
        },
      ],
      addBox: false,
      page:1,
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1;
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    handleClose(done) {
      done();
    },
    handleAddClick() {
      this.addBox = true;
    },
  },
};
</script>

<style scoped>
::v-deep .el-table th,
::v-deep .el-table td {
  text-align: center !important;
}
.icon {
  margin: 10px 0px;
}
.edit {
  padding: 20px;
}
</style>
