    华能海南昌江核电有限公司2021年应届高校毕业生招聘公告

  一、公司简介
           华能海南昌江核电有限公司由中国华能集团有限公司（华能核电开发有限公司）、中国核工业集团有限公司（中国核能电力股份有限公司）分别出资51%和49%组建
        。公司于2019年6月20日注册成立，是海南昌江核电厂二期项目两台“华龙一号”压水堆核电机组的投资、建设和运营管理主体。
           作为海南省最大的电力工程投资项目，华能海南昌江二期项目规划建设两台“华龙一号”压水堆核电机组，单台机组名义电功率约为120万千瓦，总投资约368.5亿元
        两台机组自2019年11月18日作为海南省第七批集中开工项目全面启动以来，已于今年9月2日通过国务院核准，目前正全力推进核岛筏基第一罐混凝土浇筑（FCD）前的
        各项准备工作。随着华能海南昌江二期项目经国务院常务会议核准，华能集团正式成为国内第四家具有控股建设大型压水堆资质的核电集团，华能核电产业迈入发展的
        快车道。
        作为助推海南自由贸易区（港）建设重点项目之一，华能海南昌江二期项目是中国华能携手中核集团坚决贯彻落实习近平总书记“4•13”重要讲话和关于海南系列重要批
        示指示精神，积极响应中央号召、持续支持海南全面深化改革开放的有力举措。项目建成投运后，将对加快构建海南现代化“五网”基础设施，加快推进以核电为主力电
        源的海南岛清洁能源发电体系建设，助推美丽新海南建设，提供优质高效、持久强劲的“核动力”支撑。
  二、应聘条件
        1.2021年符合全国普通高等学校全日制培养、统招统分的应届本科及以上学历毕业生或经国家教育部认证学历学位并具有派遣资格的国（境）外留学生；
        2.学历、专业等符合招聘岗位相关要求，完成规定学业并获得相应学历、学位；
        3.成绩优良，大学英语四级或六级达到425分及以上。
        4.遵纪守法、品行端正、诚实守信、爱岗敬业、身体健康，有较强的奉献精神和团队精神，服从工作安排，志愿为华能核电事业发展贡献力量；
        5.无色盲色弱；
        6.在校期间无违纪等处分；
        7.未与其他单位签订就业协议。
  三、招聘岗位及专业
       （一）招聘岗位
        安全管理、工程管理、设计采购、生产准备、综合管理等相关岗位。
       （二）招聘专业
        核工程、核技术、自动化、汉语言文学、计算机、档案等相关专业。
  四、招聘程序
        1.网上报名。应聘人员登录中国华能集团公司招聘网站，网址：http://zhaopin.chng.com.cn，根据要求填写相关信息。
        报名时间为：2020年9月25日至2020年12月31日。
        2.筛选简历。按照招聘专业及有关条件要求，筛选应聘人员简历，确定参加测评及面试人选。
        3.在线素质测评与视频面试。对简历筛选通过人员进行在线素质测评，测评通过人员以视频形式进行面试。
        4.体检与考察。组织考试人员到指定医院参加体检，对人选在校表现及综合素质等进行全面考察。
        5.公示。将合格人选通过公司网站进行公示，接受社会监督，公示期5个工作日。
        6.确定拟录用人选。综合考试考察情况，结合体检结果，确定拟录用人选。
        7.签订协议。通知拟录用人选签订三方就业协议。
        8.办理入职手续：拟录人选毕业后，查验有关证件，办理入职手续，签订劳动合同。
  五、应聘注意事项
        1.本次招聘简历填写必须真实、完整、准确，因隐瞒事实，提供虚假信息带来的不良后果，由应聘人员本人承担。
        2.身份证号为简历的唯一标识。个人电子邮箱和手机号码是通知考试的主要方式，请保持通讯方式畅通。
        3.招聘工作进度将在报名网站实时更新，未入围的应聘人员，不再另行通知。
        4. 联系方式
        联系人：刘女士
        联系电话：0898-26921027
        地址：海南省昌江黎族自治县海尾镇塘兴村
        邮政编码：572733

