    机械科学研究总院集团有限公司2021年 校园招聘公告

  一、集团简介：

         机械总院集团的前身是机械工业部机械科学研究院，成立于1956年。1999年原机械工业部直属的从事基础共性技术研究与开发的一批研究所划入机械总院整体转制为
       中央直属企业，2003年机械总院成为国资委监管的中央直属科技型企业集团。2006年更名为机械科学研究总院。2017年，机械科学研究总院公司制改制，更名为机械科
       学研究总院集团有限公司。
         机械总院集团实行以产权为纽带的母子公司式集团管理体制，总部定位为战略中心、投资中心和管理中心，各直属单位定位为技术中心、利润中心。截止目前，集团
       拥有哈尔滨焊接研究院有限公司、沈阳铸造研究所有限公司、郑州机械研究所有限公司、武汉材料保护研究所有限公司、中机第一设计研究院有限公司、北京机械工业
       自动化研究所有限公司、北京机电研究所有限公司、中机寰宇认证检验有限公司、机科发展科技股份有限公司、中机生产力促进中心、北京机科国创轻量化科学研究院
       有限公司、机械科学研究总院江苏分院有限公司、机械科学研究总院海西（福建）分院有限公司、机械科学研究总院青岛分院有限公司、工研资本控股股份有限公司、
       中机智能装备创新研究院（宁波）有限公司、云南省机械研究设计院、山西省机电设计研究院有限公司等18家全资（控股）子公司， 11个职能管理部门以及物业服务
       中心、机械工业档案馆2家直管单位。
         机械总院集团集聚了我国装备制造业制造技术（基础共性技术）研究开发最大的团队，多专业最综合的技术力量，形成了强大的技术优势和市场竞争力。集团目前拥
       有正式员工6700 余名，其中科研人员占比70% 以上，是我国装备制造业制造技术研究体系最完整、规模最大的开发研究团队。建有4 个国家级重点实验室、4个国家级
       工程研究中心、30个国家级研发基地、43 个行业和省部级创新平台, 累计取得各类科研成果7000 余项，广泛应用于国民经济和国防安全各重要领域，有力支撑了我国
       工业建设和现代制造业发展，是我国创新体系重要的组成部分。

  二、应聘基本条件：

       1.相关专业统招统分优秀应届毕业生；

       2.综合素质高，具有较强的专业能力及处理问题的能力；

       3.身体健康。

  三、招聘岗位

      需求岗位：电气工程师、机械工程师、软件开发工程师、液压控制工程师、电子电路设计工程师、数据库开发工程师、物流规划师、ERP实施顾问、ERP研发工程师、
            材料工程师、给排水设计工程师、结构设计工程师、电气设计工程师、暖通工程师、编辑、党务管理、财务管理、人力资源管理、行政管理等。

      学历要求：博士研究生/硕士研究生/大学本科。

