<template>
<div class="five">
    <h1>简历审核意见</h1>
    <Form ref="formDynamic" :model="formDynamic" :label-width="150" style="width: 600px">
        <FormItem
                v-for="(item, index) in formDynamic.items"
                v-if="item.status"
                :key="index"
                :label="'审核意见 ' + item.index"
                :prop="'items.' + index + '.value'"
                :rules="{required: true, message: '审核意见 ' + item.index +' 不能为空', trigger: 'blur'}">
            <Row>
                <Col span="18">
                    <Input v-model="value6" type="textarea" :rows="4" placeholder="Enter something..." />
                </Col>
                <Col span="4" offset="1">
                    <Button @click="handleRemove(index)">删除</Button>
                </Col>
            </Row>
        </FormItem>
        <FormItem>
            <Row>
                <Col span="12">
                    <Button type="dashed" long @click="handleAdd" icon="md-add">新增意见</Button>
                </Col>
            </Row>
        </FormItem>
        <FormItem>
            <Button type="primary" @click="handleSubmit('formDynamic')">提交</Button>
            <Button @click="handleReset('formDynamic')" style="margin-left: 8px">重置</Button>
        </FormItem>
    </Form>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                index: 1,
                formDynamic: {
                    items: [
                        {
                            value: '',
                            index: 1,
                            status: 1
                        }
                    ]
                }
            }
        },
        methods: {
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            },
            handleAdd () {
                this.index++;
                this.formDynamic.items.push({
                    value: '',
                    index: this.index,
                    status: 1
                });
            },
            handleRemove (index) {
                this.formDynamic.items[index].status = 0;
            }
        }
    }
</script>
<style scoped>
.five{
    width: 600px;
    height: 300px;
    border: 1px solid gray;
    margin: 90px auto;
    border-radius: 10px 10px 10px  10px;
}
h1{
    width: 600px;
    line-height: 50px;
    text-align: center;
}
</style>
